/* ----- Navigation -------------------- */
/* -- Bread crumbs ------------------ */
.bread-crumbs {
  padding: 0;
  list-style-type: none;
  display: none;

  @include tabl {
    display: block; }

  a {
    color: #abd62a;
    text-decoration: none;
    &:hover {
      text-decoration: underline; } }

  li {
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.4px;
    text-align: left;
    color: #ffffff;

    &:before {
      content: '/';
      margin: 0 5px; }

    &:first-child {
      &:before {
        display: none; } } }

  &_st {
    a {
      font-weight: 500;
      color: #abd62a;
      &:hover {
        color: #abd62a; } }
    li {
      color: #1a1a1a;
      &:before {
        content: '/';
        margin: 0 4px 0 2px; } } } }

.inner-cap-page {
  background-color: #fafafa;
  padding: 23px 0; }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .inner-cap-page {
    padding-left: 70px; }
  /* end media min width 1000 pixels */ }


/* -- Pagination -------------------- */
.pagination {
  list-style-type: none;
  text-align: center;
  li {
    display: inline-block;
    vertical-align: middle;
    margin: 10px 2px;
    font-size: 12px;
    font-weight: 300;
    color: #2c2c2c; }

  a {
    display: block;
    width: 28px;
    height: 28px;
    border-radius: 2px;
    background-color: $cl-gallery;
    line-height: 28px;
    text-align: center;
    text-decoration: none;
    border-radius: 3px;
    border: solid 1px #dddddd;
    background-color: #fff;
    color: #2c2c2c;
    &:hover {
      background-color: #5b8c04;
      box-shadow: 0px 3px 17px 0 rgba(0, 0, 0, 0.17);
      background-image: linear-gradient(to top, #5b8c04, #8fb300);
      color: #fff; } }

  &__prev,
  &__next {
    a {
      width: 20px;
      height: 20px;
      border-radius: 0;
      background: none;
      line-height: 20px;
      border: none;
      .fa {
        position: relative;
        top: 2px;
        left: 0;
        transition: ease-in-out 0.1s; }


      &:hover {
        background: none;
        color: #000;
        box-shadow: none; } } }

  &__prev {
    a {
      &:hover {
        .fa {
          left: -3px; } } } }

  &__next {
    a {
      &:hover {
        .fa {
          left: 3px; } } } }

  &__active {
    a {
      background-color: #5b8c04;
      color: #fff;
      box-shadow: 0px 3px 17px 0 rgba(0, 0, 0, 0.17);
      background-image: linear-gradient(to top, #5b8c04, #8fb300); } } }



/* -- Main menu --------------------- */
.menu-open-wrapper-page {
  overflow: hidden;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0; }

.burger-menu {
  display: block;
  position: absolute;
  right: 5px;
  top: 83px;
  z-index: 101;
  cursor: pointer;
  padding: 10px; }

.burger-menu-box {
  display: block;
  width: 30px;
  padding: 8px 0; }

.burger-menu-inner {
  display: block;
  position: relative;
  height: 3px;
  background-color: $burger-fill;
  border-radius: 3px;
  transition: transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19);

  &:before,
  &:after {
    content: ' ';
    position: absolute;
    left: 0;
    height: 3px;
    background-color: $burger-fill;
    width: 100%;
    border-radius: 3px;
    transition-timing-function: ease;
    transition-duration: .15s;
    transition-property: transform; }

  &:before {
    top: -8px;
    transition: top 75ms ease .12s, opacity 75ms ease; }

  &:after {
    bottom: -8px;
    transition: bottom 75ms ease .12s, transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19); } }

.menu-burger--opened {
  .burger-menu {
    position: fixed; }

  .burger-menu-inner {
    transition-delay: 0.12s;
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: rotate(45deg);

    &:before {
      top: 0;
      transition: top 75ms ease, opacity 75ms ease 0.12s;
      opacity: 0; }

    &:after {
      bottom: 0;
      transition: bottom 75ms ease, transform 75ms cubic-bezier(0.215, 0.61, 0.355, 1) .12s;
      transform: rotate(-90deg); } }

  .burger-menu-inner, .burger-menu-inner:after, .burger-menu-inner:before {
    background-color: $burger-fill-open; } }

.menu-wrapper-fixed {
  position: fixed;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 100%;
  left: -100%;
  transition: left 0.4s cubic-bezier(0.000, 0.995, 0.990, 1.000); }

.menu-open-wrapper {
  padding: 80px 20px 30px;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  transition: background 0.25s ease-out; }

.menu-burger--opened {
  .menu-wrapper-fixed {
    left: 0; }

  .menu-open-wrapper {
    background-color: #f2f2f2; }

  .menu-item {
    margin-left: 0;
    @for $i from 1 through 10 {
      &:nth-child(#{$i}) {
        transition-delay: (0.06s * $i) - 0.06s; } } } }

.header-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  min-height: 100%; }

.menu {
  margin: 0;
  padding: 0;
  list-style: none; }

.menu-item {
  position: relative;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0;
  list-style-type: none;
  font: $font-menu-item-mobile;
  color: #171717;
  width: 100%;
  margin-left: -100%;
  transition: all 0.4s cubic-bezier(0.000, 0.995, 0.990, 1.000);

  &__link {
    color: #171717;
    text-decoration: none;

    &:hover {
      color: #171717; } } }


/* - Drop down main menu -------- */
.menu-item__link_dd-open {
  padding-left: 20px;
  &:after {
    content: ' ';
    @include corner-bottom(5px, 5px, 5px, #171717);
    border-top-color: #171717;
    margin: 0 0 0 10px; } }

.menu-dd {
  display: none;
  margin: 0;
  padding: 5px 0 0;
  list-style: none; }

.menu-dd-item {
  line-height: 20px;
  margin: 10px 0;
  position: relative;

  &__link {
    color: #fff;
    text-decoration: none;
    font-size: 18px;

    &:hover {
      color: #fff; } } }

.menu-item_dd-opened {
  .menu-dd {
    display: block; }

  .menu-item__link_dd-open {
    &:after {
      transform: rotate(180deg); } } }


/* Min width 375 pixels */
@media only screen and (min-width: 375px) {
  .burger-menu {
    top: 74px; }
  /* end media min width 375 pixels */ }



/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .menu-open-wrapper-page {
    overflow: visible;
    position: relative;
    height: auto;
    width: auto; }

  .menu-wrapper-fixed {
    position: relative;
    left: 0;
    top: 0;
    height: auto;
    transition: none;
    width: 100%; }

  .menu-open-wrapper {
    background: none;
    padding: 0;
    height: auto;
    overflow: visible; }

  .burger-menu {
    display: none; }

  .header-nav {
    display: block; }

  .menu {
    text-align: center;
    border-radius: 5px; }

  .head-menu {
    margin-left: 170px;
    padding-top: 10px;
    margin-right: 40px; }

  .menu-item {
    display: inline-block;
    vertical-align: middle;
    font-weight: normal;
    font-size: 14px;
    width: auto;
    margin: 0;

    &__link {
      display: block;
      padding: 3px 5px;
      border-bottom: 1px solid transparent;
      transition: ease-in-out 0.2s;

      &:hover {
        border-bottom-color: #171717; } } }

  /* - Drop down main menu -------- */
  .menu-item_dd-opened {
    .menu-dd {
      display: none; }

    .menu-item__link_dd-open {
      &:after {
        transform: rotate(0); } } }

  .menu-dd {
    position: absolute;
    display: none;
    top: 100%;
    left: 0;
    min-width: 200px;
    text-align: left;
    padding: 10px 15px;
    border-radius: 0 0 5px 5px; }

  .menu-dd-item {
    margin: 15px 0;

    &__link {
      white-space: nowrap;
      &:hover {
        text-decoration: underline; } } }

  .menu-item {
    &:hover {
      .menu-item__link {}

      .menu-dd {
        display: block; } } }
  /* end media min width 1000 pixels */ }



/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .menu-item__link {
    padding: 3px 10px; }
  .head-menu {
    padding-top: 19px; }
  .head-menu {
    margin-left: 230px; }
  /* end media min width 1200 pixels */ }

