/* ----- Footer ------------------------ */
.footer {
  padding: 20px 0;
  background-color: #000000; }

.f-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  li {
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.35px;
    text-align: left;
    color: #ffffff; }
  a {
    color: #fff;
    text-decoration: none;
    border-bottom: 1px solid rgba(#fff, 0);
    &:hover {
      text-decoration: none;
      color: #fff;
      border-color: rgba(#fff, 0.6); } } }

/* ----- END Footer -------------------- */
