/* ----- Inner ------------------- */

.icon-txt {
  display: flex;
  justify-content: flex-start;
  margin: 20px 0;

  &__icon {
    flex-basis: 60px;
    max-width: 60px;
    margin-right: 15px;
    text-align: center;
    padding-top: 10px;
    flex-shrink: 0; }
  &__content {
    flex-grow: 1;
    b {
      text-transform: uppercase;
      font-weight: 500;
      font-size: 18px;
      letter-spacing: 0.36px;
      color: #1a1a1a;
      line-height: 22px;
      margin-bottom: 10px;
      display: block; }
    p {
      margin-bottom: 20px;
      font-size: 14px;
      line-height: 1.43;
      letter-spacing: 0.35px;
      text-align: left;
      color: #3a3a3a; } } }

.main-tt {
  position: relative;
  margin-bottom: 30px;
  &__duplicate {
    display: none;
    position: absolute;
    left: 50%;
    top: 50%;
    font-family: $site-font-family-3, serif;
    font-weight: normal;
    transform: translate(-50%, -50%);
    z-index: -1;
    font-size: 50px;
    font-weight: normal;
    line-height: 1;
    letter-spacing: 1.8px;
    color: #efefef;
    text-transform: uppercase; }


  .title-site, h1, h2, h3 {
    margin-bottom: 0;
    padding: 0 0 8px;
    border-bottom: 1px solid #6c6c6c;
    text-transform: uppercase;
    position: relative;
    display: inline-block;
    &:before {
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 25%;
      height: 3px;
      background-image: linear-gradient(to top, #5b8c04, #8fb300); } }
  &_no-upp {
    .title-site, h1, h2, h3 {
      text-transform: none; } } }

.skills-tabs {
  overflow: auto;
  display: flex;
  justify-content: space-between;
  padding-bottom: 28px;
  border-bottom: 2px solid #e8e8e8;
  margin-bottom: 40px;
  &-item {
    margin-right: 15px;
    padding: 5px 10px;
    border-radius: 10px;
    border: solid 1px transparent;
    background-color: #ffffff;
    text-align: center;
    position: relative;
    cursor: pointer;
    &.current {
      border: solid 1px #bbbbbb;
      cursor: default;
      &:after {
        content: ' ';
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        height: 4px;
        margin-top: 25px;
        border-radius: 2px;
        background-color: #5b8c04;
        background-image: linear-gradient(to top, #5b8c04, #8fb300); } }

    &__icon {
      min-height: 83px;
      margin-bottom: 20px;
      line-height: 70px; }
    &__name {
      font-size: 14px;
      font-weight: 500;
      line-height: 1.2;
      letter-spacing: 0.5px;
      color: #1a1a1a; } } }

.section-tabs {
  &_skills {
    p {
      font-size: 14px;
      line-height: 1.3;
      letter-spacing: 0.35px;
      color: #3a3a3a; } } }

.section-skills {
  background-color: #f6f6f6;
  padding: 40px 0;
  overflow: hidden; }

.skills-box__tt {
  font-family: $site-font-family-2, Arial, sans-serif;
  font-size: 30px;
  font-weight: normal;
  line-height: 1.1;
  letter-spacing: 4.4px;
  color: #1a1a1a;
  margin-bottom: 20px;
  padding-left: 50px;
  position: relative;
  &:before {
    content: ' ';
    top: 50%;
    position: absolute;
    height: 1px;
    left: 0;
    width: 45px;
    background-image: linear-gradient(to top, #5b8c04, #8fb300);
    background-color: #5b8c04; } }

.skills-box__content {
  margin-bottom: 30px;
  p {
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: 0.35px;
    color: #3a3a3a; } }

.skills-price__value {
  font-size: 46px;
  font-weight: bold;
  line-height: 1;
  letter-spacing: 1.65px;
  color: #1a1a1a; }

.skills-price__value_gradient {
  background: linear-gradient(to bottom, #5b8c04, #8fb300), linear-gradient(to bottom, #1a1a1a, #1a1a1a);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.skills-price-box {
  display: flex;
  justify-content: space-between;
  .skills-price {
    width: 48%; } }

.skills-price {
  text-align: center;
  margin-bottom: 30px;
  &-bl {
    background-image: linear-gradient(to bottom, #5b8c04, #efffaf 79%);
    max-width: 195px;
    margin: 0 auto;
    font-size: 22px;
    line-height: 24px;
    padding: 20px 15px 0;
    text-transform: uppercase;
    letter-spacing: 0.55px;
    color: #f4f6ef;
    z-index: 1;
    position: relative;
    &__img {
      margin-left: -60px;
      margin-right: -60px;
      >img {
        position: relative;
        bottom: -60px; } } } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .skills-price__value {
    font-size: 66px; }
  .section-tabs {
    &_skills {
      p {
        line-height: 1.7; } } }
  .icons-txt {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    .icon-txt {
      width: 33.3%; } }

  .icon-txt__icon {
    padding-left: 20px;
    flex-basis: 80px;
    max-width: 80px;
    padding-top: 0; }
  .main-tt {
    margin-left: auto;
    margin-right: auto;
    &__duplicate {
      display: block; } }

  .main-tt .title-site, .main-tt h1, .main-tt h2, .main-tt h3 {
    padding-top: 5px; }

  .main-tt__duplicate.main-tt__duplicate_style2 {
    transform: translate(-16%, -50%); }

  .main-tt__duplicate.main-tt__duplicate_style3 {
    transform: translate(-147%, -50%); }

  .skills-box {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &__content {
      width: 300px;
      margin-right: 30px;
      flex-shrink: 0;
      flex-basis: 300px;
      max-width: 300px; }
    &-box {
      flex-grow: 1; } }

  .section-skills {
    overflow: visible; }

  .skills-price_one {
    margin-top: -130px; }

  .skills-price-bl__img {
    margin-left: -30px;
    margin-right: -30px; }

  .skills-box__tt {
    font-size: 32px; }

  .skills-box__content {
    height: 100%;
    margin-bottom: 0; }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .skills-tabs-item__name {
    font-size: 20px; }
  .icon-txt {
    padding-right: 20px;
    &:nth-child(3n) {
      padding-right: 0; } }
  .main-tt {
    max-width: 962px;
    margin-bottom: 40px;
    &_push-md {
      margin-bottom: 82px; } }

  .main-tt .title-site, .main-tt h1, .main-tt h2, .main-tt h3 {
    padding-bottom: 12px;
    padding-top: 10px; }

  .main-tt__duplicate {
    font-size: 72px;
    transform: translate(-14%, -50%); }
  .main-tt__duplicate.main-tt__duplicate_style2 {
    transform: translate(-28%, -50%); }
  .skills-tabs-item {
    margin-right: 35px;
    padding-top: 15px; }
  .skills-tabs {
    margin-bottom: 70px; }

  .skills-box__content {
    flex-basis: 500px;
    max-width: 500px; }

  .skills-box__tt {
    padding-left: 95px;
    font-size: 44px;
    margin-bottom: 45px;
    &:before {
      left: -15px;
      width: 95px; } }
  .skills-box__content {
    p {
      margin-bottom: 55px; } }

  .skills-box__btn-wrapp {
    max-width: 360px; }

  .section-st_hidden {
    overflow: hidden; }

  .main-tt__duplicate.main-tt__duplicate_style3 {
    transform: translate(-123%, -50%); }

  .skills-tabs-item {
    &:last-child {
      margin-right: 0; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .main-tt__duplicate.main-tt__duplicate_style2 {
    transform: translate(-18%, -50%); }

  .main-tt_push {
    margin-bottom: 70px; }

  .section-skills {
    padding-top: 53px; }

  .skills-price-box {
    padding-left: 84px; }

  .skills-price_one {
    margin-top: -170px;
    margin-right: 93px;
    .skills-price-bl {
      height: 400px; }
    .skills-price-bl__img {
      margin-left: -130px;
      margin-right: -110px; } }

  .skills-price_two {
    margin-top: -107px;
    .skills-price-bl {
      height: 330px; }
    .skills-price-bl__img {
      margin-left: -130px;
      margin-right: -140px;
      >img {
        bottom: -117px; } } }

  .main-tt__duplicate.main-tt__duplicate_style3 {
    transform: translate(-130%, -50%); }

  .section-st_push-bt-md {
    padding-bottom: 70px; }
  /* end media min width 1200 pixels */ }

/* ----- END Inner --------------- */
