/* ----- About ------------------- */

.inner-cap-page {
  .main-tt {
    margin-bottom: 0; }
  .my-tt {
    margin-bottom: 0; }
  &_txt {
    padding-left: 0; } }

.about-container {
  max-width: 970px;
  margin: 0 auto;
  padding: 20px 0; }

.card-about {
  background: #444540 url(../img/about1.jpg) center center no-repeat;
  background-size: cover;
  padding: 30px 20px;
  margin-bottom: 20px;
  text-align: center;
  color: #fff;
  p {
    color: #f5f4f4;
    font-size: 12px;
    font-style: italic;
    line-height: 18px;
    letter-spacing: 0.4px; }
  .title-site, h1, h2, h3 {
    color: #fff; } }

.about-txt {
  > p {
    color: #3a3a3a;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.35px; } }

.skills-items {
  border-top: 1px solid #e3e3e3;
  border-bottom: 1px solid #e3e3e3; }

.skills-item {
  margin: 30px 0;
  text-align: center;
  &__txt {
    color: #1a1a1a;
    line-height: 24px;
    b {
      display: block;
      font-size: 16px;
      letter-spacing: 0.4px;
      color: #000; }
    span {
      font-size: 14px;
      font-weight: 300;
      letter-spacing: 0.35px; } } }

.skills-item__icon {
  position: relative;
  min-height: 75px;
  .icon {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    &:after {
      content: ' ';
      position: absolute;
      left: 50%;
      width: 1px;
      background-color: #6b6b6b;
      background-image: linear-gradient(to top, #6b6b6b 0%, #b1b1b1 100%);
      height: 27px;
      top: 100%;
      margin-top: 9px; } } }

.skills-item {
  &_even {
    .skills-item__icon {
      .icon {
        top: auto;
        bottom: 0;
        &:after {
          background-image: linear-gradient(to top, #5b8c04 0%, #8fb300 100%);
          bottom: 100%;
          top: auto;
          margin-top: 0;
          margin-bottom: -5px; } } } } }

.about-img-box {
  img {
    margin-bottom: 30px; }

  .bodoni-txt {
    text-align: center;
    max-width: 730px;
    margin: 0 auto; } }

.bodoni-txt {
  color: #686868;
  font-size: 24px;
  font-family: $site-font-family-3, serif;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0.6px; }


/* Min width 600 pixels */
@media only screen and (min-width: 600px) {
  .skills-items {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    padding: 34px 0 20px; }

  .skills-item {
    width: 50%;
    margin: 10px 0;
    &__txt {
      min-height: 75px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center; }
    &__line {
      margin-left: -10px;
      margin-right: -10px;
      >img {
        width: 100%;
        height: 40px; } } }

  /* end media min width 600 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .about-container {
    padding: 40px 0; }
  .card-about {
    padding-top: 50px;
    p {
      font-size: 16px;
      line-height: 24px; } }
  .grid-about {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
    &__half-col {
      width: 50%;
      padding-left: 15px;
      padding-right: 15px; } }

  .about-txt {
    > p {
      line-height: 24px; } }

  .grid-about {
    margin-top: 50px;
    margin-bottom: 30px; }
  .inner-cap-page_txt {
    padding: 35px 0; }
  .about-img-box img {
    margin-bottom: 40px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .skills-item {
    width: 25%;
    &__txt {
      min-height: 75px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center; }
    &__line {
      margin-left: -10px;
      margin-right: -10px;
      >img {
        width: 100%;
        height: 40px; } } }
  .about-img-box img {
    margin-bottom: 60px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .grid-about {
    margin-top: 68px;
    margin-bottom: 46px; }
  .card-about {
    padding: 57px 40px 40px; }
  /* end media min width 1200 pixels */ }

/* ----- END About --------------- */
