/* ----- Banner st ------------------- */

.banner-st-box {
  background: #ccc url(../img/banner.jpg) center center no-repeat;
  background-size: cover; }

.banner-st-wrapp {
  padding: 20px 0;
  text-align: right; }

.banner-st {
  box-shadow: 0px 3px 17px 0 rgba(0, 0, 0, 0.22);
  background-color: rgba(#f4f4f4, 0.9);
  padding: 30px 10px;
  text-align: center;
  max-width: 575px;
  margin-left: auto;
  p {
    font-size: 14px;
    line-height: 1.5;
    letter-spacing: 0.4px;
    color: #1a1a1a; }
  .btn {
    margin-top: 40px; }

  &__tt {
    font-size: 30px;
    line-height: 1;
    font-family: $site-font-family-3;
    letter-spacing: 1.5px;
    color: #1a1a1a;
    margin-bottom: 20px;
    span {
      display: inline-block;
      padding: 10px;
      position: relative;
      z-index: 1;
      &:after {
        content: ' ';
        position: absolute;
        bottom: -10px;
        right: -10px;
        background: url(../img/triangle.png) center center no-repeat;
        width: 142px;
        height: 62px;
        z-index: -1;
        background-size: contain; } } } }




/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .banner-st {
    padding: 50px 70px 65px;
    p {
      font-size: 16px; }
    &__tt {
      font-size: 60px;
      margin-bottom: 25px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .banner-st-wrapp {
    padding-right: 30px;
    padding-left: 30px; }
  /* end media min width 1000 pixels */ }

/* ----- END Banner st --------------- */
