/* ----- Header ------------------------ */
.header {
  position: relative;
  box-shadow: 0px 3px 16px 0 rgba(0, 0, 0, 0.15);
  background-color: #f2f2f2; }

.logo {
  max-width: 150px;
  margin-right: 10px;
  float: left;
  a {
    display: block;
    outline: none; } }

.header-cap {
  background-color: #191919;
  padding: 5px 0;
  position: relative;
  z-index: 120;

  &-inner {
    display: flex;
    justify-content: space-between;

    &__info {
      width: 60%;
      max-width: 270px;
      padding-right: 10px; }
    &__phone {
      width: 40%;
      text-align: right;
      max-width: 270px;
      padding-left: 10px; }

    &__search {
      display: none; } } }

.point-icon {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  font-size: 10px;
  line-height: 14px;
  position: relative;
  padding-left: 15px;
  &:hover {
    color: #fff; }
  .fa, .icon {
    position: absolute;
    top: 1px;
    left: 0;
    font-size: 10px; }

  .fa-map-marker {
    left: 3px; }

  .fa-phone {
    left: 3px; } }

.basket-bl {
  float: right;
  padding-top: 5px;
  margin-right: 50px; }

.basket-link {
  display: block;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  box-shadow: 0px 3px 17px 0 rgba(0, 0, 0, 0.17);
  background-image: linear-gradient(to top, #5b8c04, #8fb300);
  position: relative;
  transition: ease-in-out 0.1s;
  &:hover {
    box-shadow: 0px 3px 17px 0 rgba(0, 0, 0, 0.57); }
  &__value {
    display: block;
    position: absolute;
    top: 0;
    right: -8px;
    width: 19px;
    height: 19px;
    border: solid 1px #529901;
    background-color: #ffffff;
    border-radius: 50%;
    text-align: center;
    line-height: 19px;
    font-weight: 300;
    font-size: 12px;
    color: #3a3a3a; }
  .icon {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto; } }

.header-middle {
  padding: 15px 0; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .header-cap-inner__search {
    display: block;
    flex-basis: 300px;
    min-width: 300px;
    flex-shrink: 0;
    max-width: 300px; }
  .header-cap-inner {
    align-items: center; }
  .point-icon {
    font-size: 12px;
    padding-left: 19px;
    .fa {
      font-size: 12px; } }
  .search-box {
    position: relative;
    &__field {
      background-color: transparent;
      box-shadow: none;
      height: 29px;
      border: none;
      border-bottom: 1px solid #fff;
      border-radius: 0px;
      outline: none;
      width: 100%;
      padding-left: 0;
      padding-right: 30px;
      color: #fff;
      @include placeholder-color(#fff);
      font-weight: normal;
      font-size: 12px;
      letter-spacing: 0.3px;
      &:hover, &:focus {
        border-color: #fff; } }
    &__btn {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      width: 30px;
      text-align: right;
      background-color: transparent;
      box-shadow: none;
      border: none;
      outline: none;
      color: #fff;
      font-size: 12px;
      padding: 0; } }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .basket-bl {
    margin-right: 0; }
  .header-middle {
    padding: 12px 0 10px; }
  .header-cap {
    padding: 10px 0; }
  .header {
    z-index: 110; }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .logo {
    max-width: 200px; }
  .basket-bl {
    padding-top: 11px; }
  /* end media min width 1200 pixels */ }

/* ----- END Header -------------------- */
