/* ----- Banner ------------------- */
.box.visible {
  display: block; }
.box {
  display: none; }

.section-tabs {
  overflow: hidden; }

.banner-slider-box {
  margin-left: -15px;
  margin-right: -15px; }

.btn-top {
  border-radius: 4px;
  background-color: #b7d541;
  position: relative;
  text-align: center;
  padding: 4px 10px;
  color: #ffffff;
  letter-spacing: 0.3px;
  font-size: 12px;
  line-height: 16px;
  font-weight: normal;
  display: inline-block;
  text-decoration: none;
  outline: none; }

a.btn-top {
  &:hover {
    text-decoration: none;
    background-color: darken(#b7d541, 10%); } }

.banner-content {
  padding: 20px 0;
  &__brand {
    font-size: 24px;
    line-height: 26px;
    letter-spacing: 0.6px;
    color: #15232d;
    max-width: 280px;
    padding-bottom: 25px;
    margin-bottom: 5px;
    border-bottom: 1px solid #acacac; }

  &__txt {
    font-size: 14px;
    line-height: 1.43;
    letter-spacing: 0.35px;
    color: #3a3a3a;
    margin-bottom: 20px; }

  &__price {
    max-width: 190px;
    text-align: center;
    .btn {
      margin-top: 20px;
      margin-bottom: 20px; } }

  &__value {
    font-size: 24px;
    font-weight: 900;
    line-height: 26px;
    letter-spacing: 0.6px;
    color: #87ad01;
    margin-bottom: 5px; }

  &__value-old {
    position: relative;
    display: inline-block;
    padding-left: 7px;
    padding-right: 7px;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.4px;
    color: #3a3a3a;
    margin-bottom: 10px;
    &:after {
      content: ' ';
      position: absolute;
      top: 50%;
      background-color: #b7d541;
      height: 1px;
      margin-top: -1px;
      left: 0;
      right: 0;
      z-index: 1; } }

  &__time {
    color: #000;
    letter-spacing: 0.3px;
    font-size: 12px;
    line-height: 16px; }

  .btn-top {
    margin-bottom: 5px; }
  .title-site_h1 {
    margin-bottom: 10px; } }

.banner-tabs-bl {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: flex-start;
  padding: 20px 5px;
  margin-left: -15px;
  margin-right: -15px;

  &:before {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #dae0e3;
    z-index: -1; } }

.banner-tabs {
  width: 100px;
  padding: 5px;
  height: 100px;
  line-height: 90px;
  background-color: #fff;
  margin-right: 5px;
  margin-left: 5px;
  cursor: pointer;
  box-shadow: 0px 5px 20px 0 rgba(0, 0, 0, 0.15);
  transition: ease-in-out 0.2s;
  &.current {
    box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.35);
    cursor: default; }
  &:hover {
    box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.35); } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .banner-tabs {
    width: 140px;
    height: 140px;
    line-height: 130px; }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {

  .banner-content {
    .title-site_h1, h1 {
      font-size: 30px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .section-tabs {
    position: relative;
    z-index: 1; }
  .banner-content {
    padding-right: 15px;
    position: relative;
    &__top-btn {
      top: 28px;
      position: absolute;
      left: 100%;
      min-width: 90px;
      margin-left: -15px; } }
  .banner-content-box {
    position: relative;
    z-index: 2;
    width: 320px;
    background-color: #fff; }
  .banner-slider-box {
    position: absolute;
    bottom: 0;
    top: 0;
    right: 0;
    left: 350px;
    overflow: hidden;
    .banner-slider-item {
      position: relative;
      overflow: hidden;
      height: 100%;
      >img {
        position: absolute;
        top: 50%;
        left: 20%;
        transform: translate(-20%, -50%);
        min-width: 100%;
        min-height: 100%;
        max-width: 150%; } }

    .banner-slider.owl-carousel, .owl-carousel .owl-item, .owl-carousel .owl-stage-outer, .owl-carousel .owl-stage {
      position: static;
      overflow: visible;
      height: 100%; } }
  .banner-tabs-bl {
    max-width: 490px;
    padding: 25px 10px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .banner-content-box {
    width: 435px; }
  .banner-content {
    padding-top: 52px;
    padding-left: 65px;
    padding-bottom: 25px;
    .title-site_h1 {
      margin-bottom: 12px;
      font-weight: 900;
      text-transform: uppercase;
      line-height: 1.4;
      letter-spacing: 0.75px;
      padding-right: 46px; } }
  .banner-content__top-btn {
    margin-left: -60px;
    top: 56px; }
  .banner-content__brand {
    margin-bottom: 8px;
    padding-bottom: 28px; }
  .banner-content__price {
    margin-top: 32px; }
  .banner-content__value {
    margin-bottom: 8px; }
  .banner-content__price .btn {
    margin-top: 26px;
    margin-bottom: 35px; }
  .banner-slider-box {
    left: 470px;
    left: 50%;
    margin-left: -145px; }

  .banner-tabs-bl {
    max-width: 610px;
    padding: 35px 15px 35px 0;
    margin-left: -5px;
    margin-right: 0; }
  .banner-tabs {
    width: 185px;
    height: 165px;
    line-height: 155px;
    text-align: center; }
  .banner-tabs-bl:before {
    left: 73px; }
  /* end media min width 1200 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .banner-tabs-bl {
    max-width: 690px; }
  .banner-tabs {
    width: 215px; }
  /* end media min width 1440 pixels */ }

/* Min width 1700 pixels */
@media only screen and (min-width: 1700px) {
  .banner-tabs-bl {
    max-width: 775px; }
  .banner-tabs {
    width: 230px;
    margin-right: 18px; }
  /* end media min width 1700 pixels */ }

