/*file*/
.jcf-file {
  position: relative;
  display: flex;
  align-items: center; }

// btn file
.jcf-upload-button {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 4px 20px;
  border: none;
  font: normal normal 14px/24px $site-font-family, Arial, sans-serif;
  text-align: center;
  font-weight: 700;
  text-decoration: none;
  border-radius: 16px;
  outline: none;
  cursor: pointer;
  color: #fff;
  background-color: #828282;
  min-width: 150px;
  background-image: linear-gradient(to top, #6b6b6b 0%, #b1b1b1 100%);
  box-shadow: 0 3px 17px rgba(0, 0, 0, 0.17);
  transition: ease-in-out 0.1s;
  flex-shrink: 0;
  margin-right: 15px; }

.jcf-file:hover .jcf-upload-button {
  background-color: #727272;
  background-image: linear-gradient(to top, lighten(#6b6b6b, 15%) 0%, darken(#b1b1b1, 15%) 100%);
  box-shadow: 0 0px 12px rgba(0, 0, 0, 0.27); }


// input file
.jcf-real-element {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  height: auto;
  width: 150px; }

// text file
.jcf-fake-input {
  order: 1;
  overflow: hidden;
  text-overflow: ellipsis; }
