/* ----- Slider Price ------------------- */
@import "standard/no-ui-slider.scss";

//slider-price
.noUi-target {
  border: none;
  box-shadow: none;
  background: #eeeeee;
  border-radius: 0; }

.noUi-horizontal {
  height: 4px;
  .noUi-handle {
    width: 4px;
    height: 16px;
    top: -6px;
    left: -15px;
    box-shadow: none;
    outline: none;
    border-radius: 0px;
    background-color: #659403;
    border-color: #659403;
    &:before, &:after {
      width: 20px;
      height: 20px;
      margin-top: -9px;
      margin-left: -23px;
      background: none;
      z-index: -1; }
    &:after {
      display: none; } } }

.noUi-connect {
  box-shadow: none;
  background: #659403; }

.noUi-tooltip {
  color: #000;
  border: 1px solid #000;
  height: 30px;
  line-height: 30px;
  padding: 0 8px; }

.slider-price-wrapp {
  margin-bottom: 25px;
  max-width: 300px;
  margin-top: 25px; }

.slider-price {
  position: relative;
  margin-bottom: 28px;
