 }  //   border-radius: 0 10px 10px 0

.slider-price-inputs {
  overflow: hidden;
  font-size: 14px;
  line-height: 1.29;
  letter-spacing: 0.35px;
  text-align: left;
  color: #1a1a1a;
  .price-txt {
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: 0.35px;
    text-align: left;
    color: #1a1a1a;
    flex-shrink: 0;
    padding-left: 3px; }
  input {
    height: 20px;
    padding: 0;
    color: #000;
    border: 1px solid transparent;
    border-radius: 3px;
    font-size: 14px;
    letter-spacing: 0.35px;
    text-align: left;
    color: #1a1a1a;
    text-align: right;
    padding-right: 3px; } }

.slider-price-input-one {
  float: left;
  width: 85px;
  margin-right: 30px;
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.slider-price-input-two {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  float: right;
  width: 85px; }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .slider-price-wrapp {
    margin-top: 45px;
    margin-bottom: 65px; }
  /* end media min width 1000 pixels */ }

