/* ----- Contact ------------------- */

.contact-point {
  margin-bottom: 50px;
  &-item {
    text-align: center;
    background-color: #f3f3f3;
    padding: 30px 15px;
    margin-bottom: 30px;
    &__icon {
      margin-bottom: 25px; }
    .title-site_h4, h4, h5 {
      margin-bottom: 15px; } } }

.contact-txt {
  color: #1a1a1a;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.35px; }

.my-tt {
  text-align: center;
  color: #1a1a1a;
  font-size: 30px;
  font-weight: bold;
  line-height: 1.1;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 4.4px;
  font-family: $site-font-family-2, Arial, sans-serif;
  span {
    display: inline-block;
    position: relative;
    z-index: 1;
    padding-left: 30px;
    padding-right: 30px;
    &:before, &:after {
      content: ' ';
      position: absolute;
      top: 50%;
      width: 86px;
      margin-top: -1px;
      height: 2px;
      border-radius: 1px;
      background-color: #5b8c04;
      background-image: linear-gradient(to top, #5b8c04 0%, #8fb300 100%);
      z-index: -1; }
    &:before {
      left: -86px; }
    &:after {
      right: -86px; } } }

.banner-contact {
  min-height: 277px;
  background: url(../img/contact-banner.jpg) center center no-repeat;
  background-size: cover;
  padding: 30px 20px;
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: #fff;
  &__tt {
    color: #fff;
    font-size: 36px;
    font-weight: 500;
    line-height: 1.1;
    margin-bottom: 18px; }
  &__md-txt {
    color: #fff;
    font-weight: 500;
    margin-bottom: 15px;
    font-size: 24px;
    line-height: 26px;
    letter-spacing: 0.6px; }
  &__txt {
    letter-spacing: 0.45px;
    color: #fff;
    font-size: 18px;
    line-height: 18px;
    b {
      color: #689703;
      font-weight: bold; } } }

.callback {
  padding: 50px 0;
  &__cap {
    text-align: center;
    margin-bottom: 40px;
    p {
      max-width: 570px;
      margin: 0 auto 30px; } } }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .contact-point {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &-item {
      width: 48%; } }


  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .my-tt {
    font-size: 44px;
    margin-bottom: 56px; }
  .contact-point {
    margin-top: 45px;
    margin-bottom: 0;
    &-item {
      width: 31%;
      padding-top: 52px;

      &__icon {
        min-height: 100px;
        margin-bottom: 0; } } }
  /* end media min width 768 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .contact-point {
    &-item {
      width: 340px; } }

  .callback {
    padding: 67px 0 25px;
    &__cap {
      margin-bottom: 85px;
      h2, .title-site_h2 {
        margin-bottom: 25px; } } }
  .form-callback .form-group {
    margin-bottom: 80px; }
  /* end media min width 1200 pixels */ }


/* ----- END Contact --------------- */
