/* ----- Order ------------------- */
.basket-img-box {
  // display: flex
  // align-items: center
  .basket-value {
    flex-shrink: 0;
    margin-right: 10px; }
  .basket-foto {
    flex-shrink: 0;
    display: none; } }

.basket-value {
  width: 28px;
  height: 27px;
  border-radius: 3px;
  border: 1px solid #ddd;
  text-align: center;
  line-height: 27px;
  color: #000;
  font-size: 12px;
  font-weight: 300;
  text-transform: uppercase; }

.basket-foto {
  width: 62px;
  height: 62px;
  border-radius: 10px;
  border: 1px solid #bbb;
  background-color: #fff;
  overflow: hidden;
  line-height: 62px;
  text-align: center;
  >img {
    max-height: 100%; } }

.table-cap {
  background-color: #f6f6f6;
  padding: 20px 0; }

.btn-delete {
  width: 33px;
  height: 33px;
  line-height: 33px;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  box-shadow: none;
  border: none;
  background: none;
  outline: none;
  position: relative;
  .icon {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -29px;
    margin-top: -26px;
    z-index: -1; } }

.t-td_last {
  .total {
    display: none; } }

.t-td {
  color: #1a1a1a;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.47px;
  margin-right: 10px;
  margin-bottom: 10px;
  &:first-child {
    width: 28px; }
  &_name {
    width: 215px;
    margin-right: 0;
    margin-bottom: 10px; }
  &_last {
    margin-right: 0;
    position: absolute;
    top: 10px;
    right: 0; }
  &_value {
    margin-left: auto;
    position: relative;
    right: -40px;
    margin-right: 0; }
  &_price {
    margin-left: 40px; } }

.table-body {
  .col-panel-tc {
    margin: 0; }
  .t-row {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 35px 35px 15px 0;
    position: relative;
    border-bottom: 1px solid #e7e7e7; } }

.table-cap {
  display: none; }

.table-output {
  text-align: right;
  padding: 30px 0;
  color: #1a1a1a;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0.45px;
  &__name {
    display: inline-block;
    vertical-align: middle;
    margin-right: 5px; }
  &__value {
    display: inline-block;
    vertical-align: middle; } }

.table-btns {
  padding: 10px 0 40px;
  text-align: center;
  &__reset {
    margin-bottom: 15px; }
  &__back {
    margin-bottom: 15px; } }

.btn-reset_lg {
  padding-right: 50px;
  overflow: visible;
  font-size: 18px;
  &:after {
    background: url(../img/icons/close2.png) center center no-repeat;
    right: 0;
    overflow: visible;
    margin-top: -23px; } }

.tabl-adr {
  color: #1a1a1a;
  font-size: 18px;
  line-height: 22px; }

.form-wrapp-inner-box {
  border-top: 1px solid #e7e7e7;
  padding: 40px 0; }

.main-tt_sm-push {
  margin-bottom: 25px; }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .t-td_value {
    right: auto; }
  .t-td_price {
    margin-left: 10px; }
  .table-body .t-row {
    padding-right: 45px; }
  /* end media min width 480 pixels */ }


/* Min width 600 pixels */
@media only screen and (min-width: 600px) {
  .t-td_last {
    top: 50%;
    margin-top: -12px; }
  .table-body .t-row {
    padding-right: 72px; }

  .table-output {
    padding: 30px 0;
    &__name {
      margin-right: 55px; } }

  .table-btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__reset {
      margin-bottom: 0; }
    &__back {
      margin-bottom: 0; } }

  /* end media min width 600 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .table-cap {
    display: block; }
  .t-td_last {
    top: auto;
    width: 170px;
    position: relative;
    margin-top: 0;
    margin-left: 20px;
    right: auto; }
  .t-td_last .total {
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px; }
  .table-body .t-row {
    padding-right: 0; }

  .table-basket {
    display: table;
    width: 100%;
    .table-cap {
      display: table-header-group; }
    .table-body {
      display: table-row-group; }
    .t-row {
      display: table-row;
      padding: 0;
      position: static;
      border: none; }
    .t-td, .t-th {
      display: table-cell; } }

  .t-th {
    color: #1a1a1a;
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: 0.45px;
    vertical-align: middle;
    background-color: #f6f6f6;
    &:first-child {
      padding-left: 30px; } }
  .basket-img-box .basket-foto {
    display: block; }
  .t-td:first-child {
    width: auto;
    padding-left: 0; }
  .basket-img-box {
    display: flex;
    justify-content: flex-start;
    align-items: center; }
  .t-td {
    padding: 20px 4px;
    border-bottom: 1px solid #e7e7e7;
    font-size: 15px;
    vertical-align: middle; }
  .t-th:first-child {
    padding-left: 38px; }
  .t-td_price {
    margin-left: 0; }
  .table-cap {
    background-color: transparent; }

  .table-output {
    padding: 32px 32px 32px 0;
    &__name {
      margin-right: 93px; } }

  .table-btns {
    &__back {
      margin-left: auto;
      margin-right: 40px; } }

  .form-wrapp-inner-box {
    padding: 28px 0;
    .main-tt {
      max-width: none; } }
  .table-wrapp {
    overflow: hidden; }
  .table-basket {
    position: relative; }
  .t-th {
    padding: 5px 5px;
    height: 98px; }
  .table-cap {
    position: relative;
    padding: 0;
    &:before, &:after {
      content: ' ';
      position: absolute;
      right: 100%;
      top: 0;
      height: 98px;
      width: 1500px;
      background-color: #f6f6f6; }
    &:after {
      right: auto;
      left: 100%; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .basket-foto {
    width: 98px;
    height: 82px;
    line-height: 82px; }

  .t-th {
    padding: 5px 10px;
    height: 98px; }
  .t-td {
    padding: 27px 4px;
    font-size: 16px; }
  .t-td:first-child {
    padding-left: 10px;
    width: 198px; }
  .basket-img-box .basket-value {
    margin-right: 22px; }
  .t-td_name {
    width: 275px; }
  .t-th:first-child {
    padding-left: 60px; }
  .t-td_last {
    width: auto;
    position: relative; }
  .t-td_last .total {
    margin-right: 0; }

  .t-td_last {
    padding-right: 70px;
    .btn-delete {
      margin-left: auto;
      position: absolute;
      top: 50%;
      right: 35px;
      margin-top: -17px; } }

  .table-btns {
    padding-top: 43px;
    padding-bottom: 100px;
    padding-right: 35px;
    padding-left: 60px; }
  .table-btns__back {
    margin-right: 65px; }

  .form-wrapp-inner-box {
    padding-left: 60px;
    padding-right: 60px; }
  .form-wrapp-inner-box_lg {
    padding-top: 65px;
    padding-bottom: 90px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .t-td:first-child {
    width: 198px;
    max-width: 198px; }
  .t-td_name {
    width: 335px;
    max-width: 335px; }
  .t-td_price {
    width: 161px;
    max-width: 161px; }
  .t-td_value {
    width: 226px; }
  /* end media min width 1200 pixels */ }

