/* ----- Product ------------------- */

.col-panel-tc {
  display: inline-block;
  position: relative;
  width: 96px;
  padding: 0 34px;
  height: 34px;
  margin: 10px 0;
  span {
    position: absolute;
    top: 0;
    z-index: 10;
    height: 34px;
    line-height: 34px;
    border: 1px solid #f9f9f9;
    width: 34px;
    text-align: center;
    cursor: pointer;
    font-size: 14px;
    color: #5b8c04;
    @include select-text-none;
    &:hover {
      background-image: linear-gradient(to top, #5b8c04 0%, #8fb300 100%);
      border-color: transparent;
      color: #fff;
      border: none; } }
  .minus-input {
    left: 0; }
  .plus-input {
    right: 0; }
  input {
    width: 100%;
    min-width: 25px;
    padding: 0;
    text-align: center;
    height: 34px;
    background: #fff;
    position: relative;
    border: none;
    border-top: 1px solid #f9f9f9;
    border-bottom: 1px solid #f9f9f9;
    outline: none;
    border-radius: 0;
    color: #4d4d4d;
    font-size: 14px;
    font-weight: 700;
    line-height: 25px;
    letter-spacing: 0.28px; } }

.product-section {
  padding-top: 30px; }

.thumb-carousel-card-wrapp {
  position: relative;
  z-index: 1;
  border-radius: 10px;
  border: 1px solid #bbb;
  background-color: #fff;
  padding: 40px 10px;
  margin-bottom: 30px;

  .jcf-select-slct-st {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 2; } }

.thumb-carousel-card {
  .owl-prev, .owl-next {
    bottom: -25px;
    top: auto;
    left: auto;
    right: 10px; }
  .owl-prev {
    right: 50px; } }

.thumb-carousel-card-item {
  text-align: center;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%; }

.thumb-carousel-card {
  &.owl-carousel .owl-stage, &.owl-carousel .owl-stage-outer, &.owl-carousel {
    height: 100%; }
  &.owl-carousel .owl-stage {
    display: flex; }
  &.owl-carousel .owl-item {
    float: none; } }


.thumb-carousel-list-wrapp {
  margin-left: -15px;
  margin-right: -15px;
  padding: 20px 15px;
  background-color: #dae0e3;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  overflow: hidden; }

.thumb-carousel-list {
  &.owl-carousel {
    overflow: visible; }
  &.owl-carousel .owl-stage-outer {
    overflow: visible; } }

.owl-item.active.current {
  .thumb-carousel-list-item {
    transform: scale(1.1);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2); } }

.thumb-carousel-list-item {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  border-radius: 3px;
  background-color: #fff;
  width: 100%;
  height: 100px;
  line-height: 100px;
  text-align: center;
  >img {
    display: inline-block;
    vertical-align: middle;
    max-width: 100%;
    max-height: 100%; } }

.thumb-carousel-wrapp {
  max-width: 500px;
  margin: 0 auto 40px; }

.p-model {
  color: #15232d;
  font-size: 24px;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  margin-bottom: 30px; }

.p-price-bl {
  &__inline {
    display: inline-block;
    text-align: center; } }

.product-order {
  &__cap {
    padding-bottom: 20px;
    border-bottom: 1px solid #e7e7e7; }
  &__body {
    padding-top: 40px; } }

.p-price-value {
  font-size: 32px;
  font-weight: 900;
  line-height: 40px;
  letter-spacing: 0.6px;
  color: #87ad01;
  margin-bottom: 5px;
  &-old {
    color: #3a3a3a;
    font-size: 21px;
    line-height: 40px;
    letter-spacing: 0.4px;
    position: relative;
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 10px;
    &:after {
      content: ' ';
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      margin-top: -1px;
      height: 2px;
      background-color: #b7d541; } } }

.p-order-btns {
  .p-order-btns__quantity {
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px; }
  .p-order-btns__basket {
    display: inline-block;
    vertical-align: middle; } }

.product-order__body {
  padding-bottom: 30px;
  border-bottom: 1px solid #e7e7e7; }

.product-order__footer {
  padding-top: 25px; }

.p-list {
  margin-bottom: 20px;
  li {
    color: #1a1a1a;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 0.4px; }
  a {
    display: inline-block;
    padding: 4px 20px 4px 15px;
    color: #1a1a1a;
    text-decoration: none;
    outline: none;
    &:hover {
      text-decoration: none;
      background-color: #f6f6f6; } } }

.p-btns {
  li {
    margin-bottom: 20px; } }

.product-order-box {
  margin-bottom: 50px; }

.tabs_product {
  &.tabs_product_no-rad {
    border-bottom: 1px solid #c2c2c2;
    li {
      border: none; } }
  li {
    border: 1px solid #c2c2c2;
    margin-bottom: -1px;
    background-color: #fff;
    color: #1a1a1a;
    font-size: 18px;
    font-weight: 500;
    line-height: 23px;
    padding: 15px 20px;
    text-align: center;
    letter-spacing: 0.45px;
    position: relative;
    cursor: pointer;
    &:before {
      content: ' ';
      position: absolute;
      bottom: -1px;
      left: 50%;
      right: 50%;
      height: 0px;
      background-color: #5b8c04;
      transition: left ease-in-out 0.2s, right ease-in-out 0.2s; }
    &:hover {
      &:before {
        left: 0;
        right: 0;
        height: 2px; } }
    &.current {
      background-color: #f5f5f5;
      cursor: default;
      &:before {
        left: 0;
        right: 0;
        height: 2px; } } } }

.section-tabs__box {
  padding-top: 40px;
  padding-bottom: 30px;
  border-bottom: 1px solid #e7e7e7; }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .p-order-grid {
    display: flex;
    justify-content: space-between;
    .p-btns {
      flex-shrink: 0; }
    .p-list {
      padding-right: 10px; } }
  .p-order-btns {
    display: flex;
    justify-content: space-between;
    &__quantity {
      padding-left: 40px; } }
  /* end media min width 480 pixels */ }


/* Min width 600 pixels */
@media only screen and (min-width: 600px) {
  .thumb-carousel-list-wrapp {
    margin-left: 0;
    margin-right: 0; }

  .thumb-carousel-list-item {
    height: 125px;
    line-height: 125px; }

  .tabs_product {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 50%; } }
  /* end media min width 600 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {

  .product-order-box {
    display: flex; }

  .product-card {
    flex-shrink: 0;
    min-height: 45%;
    flex-basis: 45%;
    max-width: 45%;
    padding-right: 20px; }

  .product-order {
    flex-grow: 1;
    min-height: 55%;
    flex-basis: 55%;
    max-width: 55%; }

  .thumb-carousel-wrapp {
    max-width: 600px;
    margin: 0 0 40px; }

  .thumb-carousel-list-item {
    height: 100px;
    line-height: 100px;
    overflow: hidden; }

  .product-order {
    .title-site, h1, h2, h3 {
      margin-bottom: 10px; } }
  .p-model {
    margin-bottom: 15px; }
  .product-order__cap {
    padding-bottom: 10px; }
  .product-order__body {
    padding-bottom: 20px;
    padding-top: 10px; }
  .product-order {
    p {
      color: #3a3a3a;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.35px; } }


  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .thumb-carousel-card {
    .owl-prev, .owl-next {
      width: 38px;
      height: 38px; } }
  .thumb-carousel-card .owl-prev, .thumb-carousel-card .owl-next {
    right: 22px; }
  .thumb-carousel-card .owl-prev {
    right: 86px; }
  .product-card {
    min-height: 54%;
    flex-basis: 54%;
    max-width: 54%; }

  .product-order {
    min-height: 46%;
    flex-basis: 46%;
    max-width: 46%; }
  .thumb-carousel-list-item {
    height: 122px;
    line-height: 122px; }
  .thumb-carousel-card-wrapp {
    margin-bottom: 24px; }

  .tabs_product {
    li {
      width: 25%; } }

  .section-tabs__box {
    padding-top: 50px;
    padding-bottom: 38px;
    p {
      line-height: 25px;
      font-size: 16px;
      margin-bottom: 27px; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .thumb-carousel-card-wrapp {
    padding-left: 0;
    padding-right: 0;
    padding-top: 45px;
    padding-bottom: 47px; }
  .thumb-carousel-card-wrapp .jcf-select-slct-st {
    top: 22px;
    right: 30px; }
  .thumb-carousel-list-wrapp {
    padding: 25px 15px;
    margin-left: -15px;
    background-color: transparent;
    position: relative;
    z-index: 1;
    &:after {
      content: ' ';
      position: absolute;
      bottom: 0;
      top: 0;
      right: 0;
      left: 65px;
      background-color: #dae0e3;
      z-index: -1; } }
  .product-order {
    padding-left: 40px;
    padding-top: 30px;
    .title-site, h1, h2, h3 {
      margin-bottom: 16px;
      line-height: 42px;
      letter-spacing: 0.75px;
      text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.35); } }
  .p-model {
    margin-bottom: 40px; }
  .product-order__body {
    padding-top: 36px;
    padding-bottom: 32px; }
  .product-order__footer {
    padding-top: 34px;
    padding-right: 20px; }
  .p-order-btns {
    margin-top: 50px;
    padding-right: 20px; }
  .p-list li {
    margin-bottom: 5px; }
  .product-order-box {
    margin-bottom: 46px; }
  /* end media min width 1200 pixels */ }

