/*_____________ Owl Carousel _____________________*/

@import "standard/owl-carousel";

// .main-slider-wrapp
//   .owl-carousel
//     position: static
//     .owl-item, .owl-stage, .owl-stage-outer
//       position: static

.owl-carousel_nav-hidden.owl-carousel {
  .owl-nav {
    display: none; } }

.owl-carousel_dots-hidden.owl-carousel {
  .owl-dots {
    display: none; } }

/* Navigation */
$owl-nav-wh: 28px;
$owl-nav-arr-w: 6;
$owl-nav-arr-h: 10;
$owl-nav-arr-left: url(../img/icons/arrow-left-green.svg);
$owl-nav-arr-right: url(../img/icons/arrow-right-green.svg);

.owl-prev, .owl-next {
  position: absolute;
  top: 50%;
  width: $owl-nav-wh;
  height: $owl-nav-wh;
  margin: 0;
  margin-top: -($owl-nav-wh/2);
  background-color: #eee;
  padding: 0;
  border-radius: 50%; }

.owl-prev:before, .owl-next:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  background-position: 0 0;
  background-repeat: no-repeat;

  width: $owl-nav-arr-w+(px);
  height: $owl-nav-arr-h+(px);
  margin-top: -($owl-nav-arr-h/2)+(px);
  background-size: 100% 100%; }

.owl-prev {
  left: 0;
  &:before {
    margin-left: -($owl-nav-arr-w/2+1)+(px);
    background-image: $owl-nav-arr-left; } }

.owl-next {
  right: 0;
  &:before {
    margin-left: -($owl-nav-arr-w/2-1)+(px);
    background-image: $owl-nav-arr-right; } }


.main-carousel-wrapp {
  .owl-prev, .owl-next {
    top: 0;
    bottom: 0;
    height: auto;
    transform: none;
    width: 28px;
    border-radius: 0;
    background-color: transparent;
    &:before {
      width: 28px;
      height: 28px;
      border-radius: 50%;
      background: #fff url(../img/icons/arrow-right-green.svg) center center no-repeat;
      border: solid 1px #81a902;
      background-size: auto auto;
      margin-left: -14px;
      margin-top: -14px; } }
  .owl-prev {
    &:before {
      background-image: url(../img/icons/arrow-left-green.svg); } } }


.banner-slider {
  .owl-nav {
    position: absolute;
    bottom: 0;
    top: auto;
    left: 50%;
    margin-left: -145px;
    width: 290px;
    height: 40px;
    right: auto; }
  .owl-prev, .owl-next {
    background-color: transparent;
    border: 1px solid #fff;
    z-index: 1;
    &:hover {
      background-color: #fff; } }

  .owl-prev {
    left: 35px;
    &:before {
      background-image: url(../img/icons/arrow-left-light.svg); }
    &:hover {
      &:before {
        background-image: url(../img/icons/arrow-left-dark.svg); } } }
  .owl-next {
    right: 35px;
    &:before {
      background-image: url(../img/icons/arrow-right-light.svg); }
    &:hover {
      &:before {
        background-image: url(../img/icons/arrow-right-dark.svg); } } } }

.owl-carousel_nav-grd {
  .owl-prev, .owl-next {
    background-color: #fff;
    border: 1px solid #609004;
    &:hover {
      border-color: transparent;
      background-color: #e8e8e8;
      background-image: linear-gradient(to top, #5b8c04 0%, #8fb300 100%); } }
  .owl-prev {
    &:before {
      background-image: url(../img/icons/arrow-left-slider-st.svg); }
    &:hover {
      &:before {
        background-image: url(../img/icons/arrow-left-slider.svg); } } }
  .owl-next {
    &:before {
      background-image: url(../img/icons/arrow-right-slider-st.svg); }
    &:hover {
      &:before {
        background-image: url(../img/icons/arrow-right-slider.svg); } } } }

.main-carousel-wrapp_three-col .owl-prev, .main-carousel-wrapp_three-col .owl-next {
  top: 50%;
  bottom: auto;
  width: 28px;
  height: 28px;
  background-color: #fff;
  border-radius: 50%;
  &:before {
    border: none;
    background-color: transparent;
    width: 6px;
    height: 10px;
    top: 50%;
    left: 50%;
    background-size: 100% 100%;
    border-radius: 0px;
    margin-left: -4px;
    margin-top: -5px; } }

.main-carousel-wrapp_three-col .owl-next {
  &:before {
    margin-left: -2px; } }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .main-carousel-wrapp {
    .owl-prev, .owl-next {
      left: 50%;
      right: auto;
      background-color: rgba(#fff, 0.7);
      &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        width: 30px; } }
    .owl-prev {
      margin-right: 510px;
      left: 0;
      right: 50%;
      width: auto;
      &:before {
        right: 0;
        left: auto;
        margin-right: 12px; }
      &:after {
        background: linear-gradient(to right, rgba(255,255,255, 0.7) 0%,rgba(255,255,255,0) 100%);
        left: 100%; } }

    .owl-next {
      margin-left: 510px;
      right: 0;
      width: auto;
      &:before {
        left: 0;
        right: auto;
        margin-left: 12px; }
      &:after {
        background: linear-gradient(to left, rgba(255,255,255,0.7) 0%,rgba(255,255,255,0) 100%);
        right: 100%; } } }

  /* end media min width 1200 pixels */ }


//------------------------------
/* Dots */
$owl-dot-wh: 10;
$owl-dot-margin: 5;
$wh-dots: $owl-dot-wh+($owl-dot-margin*2);

.carousel-st-wrapp {
  overflow: hidden;
  padding-bottom: 40px; }

.owl-dots {
  position: absolute;
  bottom: -30px;
  width: 100%;
  text-align: center;
  background-color: rgba(#000000, 0.6);
  width: 290px;
  left: 50%;
  bottom: 0;
  margin-left: -145px;
  padding: 15px 80px;
  display: flex;
  justify-content: center; }


.owl-dot {
  display: inline-block;
  position: relative;
  width: $owl-dot-wh+(px);
  height: $owl-dot-wh+(px);
  border-radius: 50%;
  background-color: transparent;
  margin: 0 $owl-dot-margin+(px);
  border: solid 1px #ffffff;
  flex-shrink: 0;

  // box-sizing: content-box
  // background-clip: content-box
  // border: 2px solid transparent

  &.active {
    width: $owl-dot-wh+(px);
    height: $owl-dot-wh+(px);
    background-color: #7aa402;

    // border-color: #000
    // background-clip: padding-box
    // box-sizing: padding-box
 }    // background-color: transparent

  &:before {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    width: $wh-dots+(px);
    height: $wh-dots+(px);
    margin-top: -($wh-dots/2)+(px);
    margin-left: -($wh-dots/2)+(px);
    border-radius: 50%;
    background-color: transparent; } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .owl-dots {
    padding-top: 31px;
    padding-bottom: 31px; }
  .banner-slider {
    .owl-nav {
      height: 72px; } }
  /* end media min width 480 pixels */ }

/*____________end Owl Carousel _____________________*/
