/* ----- Index page -------------------- */
.section-carousel {
  padding: 40px 0;
  overflow: hidden;
  .container_lg {
    max-width: 1650px; } }
.main-carousel-wrapp {
  margin-left: -15px;
  margin-right: -15px;
  padding: 20px 0;
  overflow: hidden;
  .owl-carousel .owl-stage-outer {
    overflow: visible; }
  .main-carousel {
    overflow: visible; }
  .owl-carousel .owl-stage {
    display: flex;
    justify-content: flex-start;
    align-items: center; }
  .owl-carousel .owl-item {
    float: none; } }

.owl-item.center {
  .prod {
    &:before {
      box-shadow: 0px 5px 20px 0 rgba(0, 0, 0, 0.2); }
    padding: 0px 20px 17px;
    &:hover {
      &:before {
        box-shadow: 0px 5px 20px 0 rgba(0, 0, 0, 0.4); } } }

  .prod__preview {
    display: block; }
  .prod__text {
    display: block; }
  .prod__img {
    margin-bottom: 7px; }
  .prod__icon {
    top: 15px;
    right: 8px; } }

.main-carousel_st {
  .owl-item.center {
    .prod {
      padding: 13px 20px 17px;
      &:before {
        top: -10px;
        bottom: -18px;
        left: 0;
        right: 0; } }
    .prod__img {
      margin-bottom: 27px; }
    .prod__icon {
      top: 15px;
      right: 15px; } }
  .prod__price {
    margin-top: auto; }
  .prod__tt {
    margin-bottom: 4px; }
  .prod {
    min-height: 336px;
    display: flex;
    flex-direction: column; } }

.main-carousel-wrapp_md {
  padding: 30px 0 40px; }

.prod {
  text-decoration: none;
  outline: none;
  display: block;
  // border-radius: 10px
  // border: solid 1px #bbbbbb
  // background-color: #ffffff
  text-align: center;
  padding: 13px 20px 17px;
  min-width: 300px;
  max-width: 300px;
  color: #1a1a1a;
  position: relative;
  z-index: 1;
  // transition: ease-in-out 0.4s
  transform: scale(1);
  // box-shadow: 0px 5px 20px 0 rgba(0, 0, 0, 0)
  perspective: 1000px;
  &:before {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    box-shadow: 0px 5px 20px 0 rgba(0, 0, 0, 0);
    background-color: #ffffff;
    border: solid 1px #bbbbbb;
    border-radius: 10px;
    transition: ease-in-out 0.2s;
    z-index: -1; }
  &:hover {
    text-decoration: none;
    outline: none;
    color: #1a1a1a;
    &:before {
      box-shadow: 0px 5px 20px 0 rgba(0, 0, 0, 0.2); } }
  &__preview {
    display: none;
    opacity: 0;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
    max-width: 280px;
    font-size: 12px;
    letter-spacing: 0.3px;
    text-align: center;
    padding: 5px 10px;
    line-height: 14px;
    border-radius: 4px;
    background-color: #8eb200;
    color: #ffffff;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    transform-origin: top center;
    animation: {
      name: rotate-x1;
      duration: 200ms;
      delay: 400ms;
      timing-function: ease-in-out;
      fill-mode: forwards; } }
  &__icon {
    position: absolute;
    top: 15px;
    right: 15px; }
  &__img {
    min-height: 220px;
    line-height: 215px;
    margin-bottom: 27px;
    >img {
      max-width: 100%; } }
  &__tt {
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: 0.45px;
    color: #1a1a1a;
    margin-bottom: 7px; }
  &__text {
    font-size: 14px;
    font-weight: 300;
    line-height: 1.43;
    letter-spacing: 0.35px;
    color: #1a1a1a;
    position: relative;
    padding: 5px 0;
    opacity: 0;
    transform-origin: center center;
    display: none;
    animation: {
      name: rotate-x;
      duration: 200ms;
      delay: 400ms;
      timing-function: ease-in-out;
      fill-mode: forwards; }
    &:after {
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 28%;
      right: 28%;
      height: 1px;
      background-color: #d0d0d0; }
    &:before {
      content: ' ';
      position: absolute;
      top: 0;
      left: 10px;
      right: 10px;
      height: 1px;
      background-color: #d0d0d0; } }
  &__price {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    letter-spacing: 0.4px;
    color: #87ad01;
    margin-top: 12px; }
  &__price-old {
    color: #3a3a3a;
    font-size: 14px;
    font-weight: 300;
    line-height: 18px;
    letter-spacing: 0.26px;
    position: relative;
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    &:after {
      content: ' ';
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      height: 1px;
      background-color: #b7d541; } } }

@keyframes rotate-x1 {
  0% {
    opacity: 0;
    transform: rotateX(-90deg) translate(-50%, -50%); }
  100% {
    opacity: 1;
    transform: rotateX(0deg) translate(-50%, -50%); } }

@keyframes rotate-x {
  0% {
    opacity: 0;
    transform: rotateX(-90deg);
 }    // max-height: 0px
  100% {
    opacity: 1;
    transform: rotateX(0deg);
 } }    // max-height: 80px

.prod-card {
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid #bbb;
  display: block;
  text-align: center;
  position: relative;
  text-decoration: none;
  outline: none;
  color: #1a1a1a;
  width: 300px;
  &:hover {
    text-decoration: none;
    color: #1a1a1a; }
  &__content {
    padding: 20px; }
  &__img {
    margin-top: -1px;
    margin-left: -1px;
    margin-right: -1px;
    position: relative;
    min-height: 226px;
    overflow: hidden;
    >img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 100%;
      min-height: 100%;
      max-width: none; } }
  &__tt {
    color: #87ad01;
    font-size: 20px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0.59px;
    margin-bottom: 10px; }
  &__txt {
    color: #1a1a1a;
    font-size: 14px;
    font-weight: 300;
    line-height: 20px;
    letter-spacing: 0.41px; } }

/* Min width 375 pixels */
@media only screen and (min-width: 375px) {
  .owl-item.center {
    .prod {
      &:before {
        transform: scale(1.07, 1); } } }
  /* end media min width 375 pixels */ }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .main-carousel-wrapp_three-col {
    padding-top: 40px;
    padding-bottom: 40px;
    .owl-item.active.center {
      .prod-card {
        transform: scale(1.1);
        box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2); } } }
  /* end media min width 480 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .section-carousel {
    padding: 60px 0;
    &_lg {
      padding-top: 80px;
      padding-bottom: 80px; } }

  .main-carousel-wrapp_three-col {
    max-width: 1040px;
    margin: 0 auto;
    overflow: visible;
    padding: 0;
    .main-carousel .owl-stage-outer {
      overflow: hidden;
      padding-top: 40px;
      padding-bottom: 40px; } }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .main-tt__duplicate.main-tt__duplicate_style4 {
    transform: translate(3%, -50%); }

  .main-carousel-wrapp_three-col {
    .main-carousel-wrapp .owl-next {}

    &.main-carousel-wrapp .owl-prev:after, &.main-carousel-wrapp .owl-next:after {
      display: none; }
    &.main-carousel-wrapp .owl-prev, &.main-carousel-wrapp .owl-next {
      margin-right: 0;
      width: 38px;
      height: 38px;
      margin-top: -19px;
      background-color: #fff; }
    &.main-carousel-wrapp .owl-prev {
      margin-left: 0;
      left: -50px;
      right: auto; }
    &.main-carousel-wrapp .owl-next {
      left: auto;
      right: -50px;
      margin-left: 0; }
    &.main-carousel-wrapp .owl-next:before {
      margin-left: -2px;
      left: 50%; }
    &.main-carousel-wrapp .owl-prev:before {
      margin-left: -4px;
      left: 50%; } }


  /* end media min width 1200 pixels */ }

/* ----- END index page /--------------- */
