/* ----- Page structure ---------------- */
.clear {
  clear: both; }

.group:after {
  content: ".";
  clear: both;
  display: block;
  height: 0;
  visibility: hidden; }

#wrapper {
  min-width: 320px; }

.section-st {
  padding: 40px 0;
  &_dark {
    background-color: #f2f2f2; } }

.section-dark {
  background-color: #f2f2f2; }

.container {
  padding-left: 15px;
  padding-right: 15px;
  width: auto; }

.visible-lap, .hidden-full, .hidden-mobile {
  display: none; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .hidden-mobile {
    display: block; }

  .hidden-tabl {
    display: block; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  html, body {
    height: 100%; }

  body {
    display: flex;
    flex-direction: column; }

  #wrapper {
    flex: 1 0 auto; }

  .footer {
    flex-shrink: 0; }

  .section-st {
    padding: 40px 0; }

  .container {
    margin: 0 auto; }

  .visible-lap {
    display: block; }

  .hidden-lap {
    display: none; }

  .section-st_sm-bt {
    padding-bottom: 20px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .section-st_lg {
    padding: 80px 0; }
  .section-st_md {
    padding: 60px 0; }
  .hidden-lap {
    display: block; }
  .container {
    max-width: 1190px; }
  /* end media min width 1200 pixels */ }

























