/* ----- Catalog ------------------- */

.section-contacts_dark {
  background-color: #f5f5f5;
  .btn_border {
    background-color: #f5f5f5;
    &:before {
      background-color: #f5f5f5; } } }

.inner-grid_push {
  margin-bottom: 50px; }

.banner-catalog {
  background: #ccc url(../img/banner2.jpg) 20% 50% no-repeat;
  background-size: cover;
  &_wrapp-bg {
    background-position: 36% 50%; } }


.banner-catalog-content {
  min-height: 300px;
  padding: 25px 0;
  color: #fff;
  &__right {
    display: none; }
  .title-site, h1, h2, h3 {
    color: #fff; } }

.text_gradient {
  color: #abd62a;
  background: linear-gradient(to top, #5b8c04, #8fb300), linear-gradient(to bottom, #abd62a, #abd62a);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.text_shadow {
  text-shadow: 0px 3px 17px rgba(0, 0, 0, 0.17); }

.inner-grid__aside_filter {
  padding-left: 23px; }

.inner-grid {
  padding: 20px 0;
  &__aside {
    margin-bottom: 40px; } }

.line-bt {
  margin-bottom: 16px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee; }

.main-filter {
  &__tt {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.35px;
    text-align: left;
    color: #1a1a1a;
    margin-bottom: 25px; }

  &-item__name {
    margin-bottom: 25px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.79;
    letter-spacing: 0.28px;
    color: #1a1a1a;
    text-transform: uppercase; }

  &-item__inner {
    margin-bottom: 35px; } }

.filter-group {
  position: relative;
  padding-right: 25px;
  line-height: 1.29;
  .jcf-checkbox {
    position: absolute;
    left: -21px;
    top: 2px; }
  &__value {
    position: absolute;
    right: 0;
    top: 3px;
    font-size: 12px;
    font-weight: 300;
    line-height: 12px;
    letter-spacing: 0.24px;
    color: #1a1a1a; }
  &__label {
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 0.28px;
    text-align: left;
    color: #1a1a1a;
    &:hover {
      color: #74af00; } }
  .jcf-label-active {
    font-weight: 500;
    color: #74af00;
    letter-spacing: 0.05px; } }

.btn-reset {
  font-size: 16px;
  font-weight: 300;
  line-height: 20px;
  letter-spacing: 0.4px;
  color: #000000;
  background: none;
  box-shadow: none;
  padding: 0;
  margin: 0;
  border: none;
  position: relative;
  z-index: 1;
  padding-right: 30px;
  outline: none;
  cursor: pointer;
  &:hover {
    text-decoration: underline; }
  &:after {
    content: ' ';
    position: absolute;
    top: 50%;
    right: -10px;
    margin-top: -19px;
    background: url(../img/icons/close.png) center center no-repeat;
    width: 42px;
    height: 42px;
    z-index: -1; } }

.filter-cap {
  padding-bottom: 10px;
  margin-bottom: 40px;
  border-bottom: 1px solid #eee;
  text-align: right;
  .jcf-select {
    text-align: left; }
  &__name {
    margin-right: 10px;
    display: inline-block;
    vertical-align: middle;
    line-height: 30px; } }

.prod-cards {
  &__item {
    text-align: center;
    margin-bottom: 30px; } }

.prod_st {
  max-width: 100%;
  min-width: 256px;
  .prod__preview {
    display: block; }
  .prod__text {
    display: block;
    font-size: 12px;
    font-weight: 300; }
  .prod__img {
    line-height: 140px;
    min-height: 140px;
    line-height: 140px;
    max-width: 180px;
    margin: 0 auto;
    letter-spacing: 0.3px;
    color: #1a1a1a;
    margin-bottom: 10px; }
  .prod__tt {
    font-size: 16px;
    letter-spacing: 0.4px; }
  .prod__price {
    margin-top: 9px;
    font-size: 14px; }
  &_price {
    .prod__price {
      font-size: 16px;
      &-old {
        margin-top: 2px; } } } }

.inner-grid__aside_filter {
  max-width: 560px;
  margin: 0 auto 50px; }

.prod-cards__item .prod {
  height: 100%; }

.filter-cap__name-bl {
  text-align: left;
  color: #1a1a1a;
  font-size: 14px;
  line-height: 25px;
  letter-spacing: 0.35px;
  margin-bottom: 10px; }

/* Min width 600 pixels */
@media only screen and (min-width: 600px) {
  .prod-cards {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    &__item {
      text-align: center;
      margin-bottom: 30px;
      padding: 0 10px;
      width: 50%;
      .prod {
        min-width: 200px;
        max-width: 100%; } } }
  /* end media min width 600 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .banner-catalog {
    background-position: left center; }

  .banner-catalog-content {
    min-height: 335px;
    display: flex;
    justify-content: flex-end;
    flex-direction: column; }

  .filter-group {
    &__label {
      line-height: 1.29; } }

  .filter-cap_grid {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .filter-cap__name-bl {
      margin-bottom: 0; } }

  .banner-catalog_wrapp4 .banner-catalog-content__right {
    .title-site, h1, h2 {
      font-size: 48px;
      padding-top: 20px; } }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .banner-catalog {
    background-position: 60% 50%;
    &_wrapp-bg {
      background-position: 71% 50%; } }
  .banner-catalog-content__right {
    display: block;
    text-align: right;
    margin-bottom: auto; }
  .banner-catalog-content {
    padding: 50px 0 28px; }
  .banner-catalog-content__right {
    .title-site_bodoni {
      letter-spacing: 1.8px; } }
  .inner-grid {
    padding-top: 30px;
    display: flex;
    justify-content: space-between;
    &.inner-grid_one-col {
      display: block; }
    &__aside {
      flex-basis: 260px;
      max-width: 260px;
      flex-shrink: 0;
      margin-right: 30px; }
    &__content {
      flex-grow: 1; } }
  .main-filter__tt {
    line-height: 30px; }

  .inner-grid_one-col {
    .prod-cards {
      &__item {
        width: 33.3%; } } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {

  .inner-grid_one-col {
    .prod-cards {
      &__item {
        width: 25%; } } }
  .banner-catalog-content__left {
    padding-left: 60px; }
  .banner-catalog {
    background-position: 50% 50%;
    &_wrapp-bg {
      background-position: 71% 50%; } }

  .inner-grid__aside {
    flex-basis: 282px;
    max-width: 282px; }
  .inner-grid__aside_filter {
    padding-left: 19px; }

  .prod-cards {
    margin-right: -12px;
    margin-bottom: 80px;
    &__item {
      text-align: center;
      margin-bottom: 0;
      padding: 0 12px;
      width: 33.3%;
      display: flex;
      justify-content: center;
      flex-direction: column;
      min-height: 305px;
      margin-bottom: 10px; } }
  .prod-cards__item .prod_st {
    height: auto; }
  .prod_st {
    min-width: 200px;
    max-width: 100%;
    padding-top: 1px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    max-height: 305px;
    min-height: 288px;
    &:before {
      transition: ease-in-out 0.1s; }
    .prod__img {
      flex-shrink: 10;
      min-height: 100px;
      width: 100%;
      height: 200px;
      flex-basis: 200px;
      line-height: 200px;
      position: relative;
      margin-bottom: 10px;
      max-width: none;
      >img {
        max-height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: -1; } }
    .prod__price {
      margin-top: auto; }
    .prod__tt {
      line-height: 18px;
      margin-bottom: 5px; }
    &:hover {
      padding-bottom: 12px;
      min-height: 305px;
      .prod__preview {
        display: block; }
      .prod__text {
        display: block; }
      .prod__img {
        margin-bottom: 5px;
        height: 180px;
        flex-basis: 180px;
        line-height: 180px; }
      .prod__tt {
        font-size: 16px;
        letter-spacing: 0.4px;
        margin-bottom: 10px; }
      .prod__price {
        margin-top: auto;
        padding-top: 5px;
        font-size: 16px; }
      &:before {
        left: -5px;
        right: -5px; }
      .prod__icon {
        top: 20px;
        right: 10px; } }
    .prod__preview {
      display: none;
      animation: {
        delay: 0ms; } }
    .prod__text {
      display: none;
      animation: {
        delay: 0ms; } } }

  .main-filter__tt {
    margin-bottom: 50px; }
  .main-filter-item__name {
    margin-bottom: 35px; }
  .main-filter-item__inner {
    margin-bottom: 44px; }
  .filter-cap {
    margin-bottom: 50px;
    &_push-sm {
      margin-bottom: 35px; } }

  .prod-cards_price-old {
    .prod-cards__item {
      min-height: 330px;
      margin-bottom: 5px; } }

  .prod_st_price {
    max-height: 330px;
    min-height: 295px;
    .prod__img {
      margin-bottom: 5px; }
    &:hover {
      min-height: 330px;
      .prod__img {
        margin-bottom: 5px;
        height: 195px;
        flex-basis: 195px;
        line-height: 195px; }
      .prod__tt {
        margin-bottom: 5px; } } }
  /* end media min width 1200 pixels */ }


/* ----- END Catalog --------------- */
