/* ----- Contact ------------------- */
.main-map {
  min-height: 320px;
  background-color: #ccc; }

/* Google MAP hidden nav */
.gmnoprint, .gm-style-cc, .gm-style button {
  display: none !important; }

.section-contacts {
  padding: 40px 0; }

.logo-second {
  margin-bottom: 30px;

  a {
    display: inline-block;
    vertical-align: top; } }

.inst-bl {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.28px;
  color: #1a1a1a;
  margin-bottom: 25px;
  &__link {
    letter-spacing: 0.28px;
    color: #1a1a1a;
    text-decoration: none;
    outline: none;
    font-style: italic;
    font-weight: 500; } }

a.inst-bl__link {
  &:hover {
    text-decoration: underline;
    color: #1a1a1a; } }

.list-pic {
  margin-bottom: 35px;
  li {
    display: inline-block;
    vertical-align: top;
    margin-right: 5px;
    &:last-child {
      margin-right: 0; } }
  &__bl {
    width: 64px;
    height: 66px;
    overflow: hidden;
    position: relative;
    display: block;
    text-decoration: none;
    outline: none;
    box-shadow: 2px 2px 6px rgba(#000, 0.3);
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 100%;
      min-height: 100%;
      max-width: 200%; } } }


.soc-bl {
  margin-bottom: 30px;
  &__tt {
    font-size: 14px;
    line-height: 18px;
    color: #1b1b1b; }
  &__list {
    margin-left: -15px;
    li {
      display: inline-block;
      vertical-align: middle; }
    a {
      outline: none;
      display: block;
      &:hover {
        opacity: 0.8; } } } }

.contact-blocks {
  li {
    margin-bottom: 30px; } }

.contact-icon {
  position: relative;
  padding-left: 25px;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.4px;
  color: #1a1a1a;
  margin-bottom: 5px;
  .icon, .fa {
    position: absolute;
    top: 2px;
    left: 0; }
  .fa {
    color: #669412;
    font-size: 16px; }
  .fa-envelope-o {
    top: 1px; }
  &_sm {
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.35px;
    .icon, .fa {
      top: 2px; }
    .fa {
      font-size: 14px; } } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .contact-grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__one {
      width: 47%;
      margin-bottom: 30px; }
    &__two {
      width: 47%;
      padding-top: 30px;
      margin-bottom: 30px; }
    &__three {
      width: 100%; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .contact-grid {
    &__one {
      width: 30%;
      margin-bottom: 30px;
      order: 2; }
    &__two {
      order: 1;
      width: 25%;
      padding-top: 20px;
      margin-bottom: 30px; }
    &__three {
      width: 40%; } }
  .contact-blocks {
    li {
      margin-bottom: 40px; } }
  .section-contacts {
    padding: 80px 0 27px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .contact-grid {
    &__one {
      width: 25%;
      margin-bottom: 10px;
      order: 2; }
    &__two {
      order: 1;
      width: 24%;
      padding-top: 20px;
      margin-bottom: 30px; }
    &__three {
      width: 42.5%; } }
  .contact-blocks {
    li {
      margin-bottom: 40px; } }
  .logo-second {
    margin-bottom: 43px; }
  .soc-bl__tt {
    margin-bottom: 5px; }
  .contact-blocks li {
    margin-bottom: 42px; }
  /* end media min width 1200 pixels */ }

/* ----- END Contact --------------- */
