/* ----- Forms ------------------------- */
.st-form {
  margin-bottom: 20px;

  &__tt {
    display: block; } }


.form-group {
  margin-bottom: 20px;

  &__label {
    display: block;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 18px; }

  &_inline {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .form-group__label {
      min-width: 140px;
      max-width: 140px;
      padding: 0 10px 0 0;
      margin: 5px 0;

      color: #1a1a1a;
      font-weight: 400;
      letter-spacing: 0.35px;
      span {
        color: #f80000; } } } }

.form-group_check {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  .form-group__label {
    order: 1; }
  .jcf-radio {
    border-radius: 3px;
    border: 1px solid #ddd;
    overflow: hidden;
    margin-right: 22px; }
  .jcf-radio span {
    background: url(../img/icons/checkbox.svg) -8px -5px no-repeat;
    border-radius: 0px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }
  .jcf-checkbox {
    margin-right: 20px;
    width: 19px;
    height: 19px;
    border-radius: 3px; }
  .jcf-checkbox span {
    width: 31px;
    height: 31px;
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    border: none;
    background-color: #000;
    margin: 0;
    background: url(../img/icons/checkbox.svg) center center no-repeat;
    background-size: 100% 100%; } }

 :root .form-group_check .jcf-checkbox span {
  margin: 3px 0 0 1px; }

.form-group_check_sm {
  .form-group__label {
    margin-bottom: 0;
    span {
      color: #6f9b02; } }
  .jcf-checkbox {
    margin-right: 14px; } }

.form-group__label-check {
  color: #1a1a1a;
  font-size: 16px;
  line-height: 22px; }

.form-callback {
  .form-group__field {
    height: 40px;
    padding-left: 0;
    font-size: 16px;
    padding-right: 0;
    font-weight: 300;
    border-left: none;
    border-right: none;
    border-top: none;
    border-radius: 0px;
    @include placeholder-color(#7b7b7b);
    color: #1a1a1a;
    border-bottom: 1px solid #949494;
    &:hover {
      border-bottom: 1px solid darken(#949494, 10%); }
    &:focus {
      border-bottom: 1px solid darken(#949494, 20%); } }

  input.btn_lg {
    height: 52px; } }

.order {
  &__body {
    padding: 30px 0 10px;
    border-bottom: 1px solid #e7e7e7; } }

.order__cap {
  padding-top: 31px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e7e7e7; }

.order-description {
  color: #1a1a1a;
  font-size: 12px;
  font-weight: 300;
  line-height: 16px;
  margin-bottom: 10px;

  span {
    color: #f80000; } }

.order__btn {
  padding: 50px 0; }

.form-group_textarea {
  max-width: 600px;
  margin-bottom: 30px; }

.form-inline {
  padding: 50px 0 0;
  .form-group_inline {
    display: block; }
  .form-group_inline .form-group__label {
    max-width: none;
    margin-bottom: 10px; } }

.form-grou-capcha {
  display: flex;
  justify-content: flex-start;
  &__field {
    flex-basis: 120px;
    flex-shrink: 0;
    margin-right: 20px;
    max-width: 120px; }
  &__btn {
    padding: 8px 25px;
    text-align: center;
    min-width: 120px;
    background-color: #d3d3d3;
    color: #000;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: 0.8px;
    position: relative;
    cursor: default;
    &:before {
      content: ' ';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 1; } } }

.form-group_btn {
  margin: 40px 0 0; }

.review-form {
  max-width: 780px;
  margin: 0 auto; }

.form-inline {
  .form-group_inline .form-group__label {
    text-align: left; } }



/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .form-group-grid {
    display: flex;
    justify-content: space-between;

    &__half-col {
      width: 48.5%; }

    &__three-col {
      width: 31.3%; }

    &__four-col {
      width: 22.8%; } }

  .form-callback {
    .form-group {
      margin-bottom: 40px; } }

  .form-group {
    &_inline {
      .form-group__label {
        min-width: 165px;
        max-width: 165px;
        padding: 0 15px 0 0;
        text-align: right; } } }

  .order__body {
    .form-group_check {
      margin-left: 168px; } }

  .form-inline {
    .form-group_inline {
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      .form-group__label {
        margin-top: 10px;
        min-width: 180px;
        flex-basis: 180px;
        max-width: 180px;
        padding-right: 10px; } } }

  .review-form__inner {
    margin-top: 40px; }

  .form-group_md-desc {
    margin-bottom: 30px; }

  .form-group_btn {
    margin-top: 55px;
    margin-bottom: 0; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .form-callback {
    .form-group__field {
      height: 50px; } }

  .form-group {
    &_inline {
      .form-group__label {
        min-width: 240px;
        max-width: 240px;
        padding: 0 65px 0 0;
        text-align: right; } } }

  .order__body {
    .form-group_check {
      margin-left: 168px; } }

  .banner-catalog_wrapp3.banner-catalog {
    .banner-catalog-content__right .title-site_bodoni {
      font-size: 48px;
      line-height: 54px;
      text-transform: uppercase;
      letter-spacing: 1.2px; } }

  .order {
    .main-tt {
      max-width: 1025px;
      margin-bottom: 35px; }
    .order-description {
      margin-left: 69px; }
    &__body {
      padding: 44px 0 25px; }
    .form-group {
      margin-bottom: 27px; } }
  .order__body .form-group_check {
    margin-left: 240px; }
  /* end media min width 1000 pixels */ }


/* ----- END Forms --------------------- */
