/* ----- Payment ------------------- */
.bodoni-txt_center {
  text-align: center;
  max-width: 860px;
  margin: 0 auto; }

.main-bl {
  padding: 30px 0; }

.br-bottom {
  border-bottom: 1px solid #eee; }

.s-icons-list {
  background-color: #f4f4f4;
  margin-left: -15px;
  margin-right: -15px;
  margin-bottom: 40px;
  &__item {
    padding: 20px 15px; } }

.s-box {
  padding-top: 40px;
  padding-bottom: 20px;
  margin-bottom: 35px;
  &_sm {
    padding-top: 20px; } }

.s-icon {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  &__name {
    color: #191919;
    font-size: 20px;
    font-weight: 300;
    line-height: 36px;
    letter-spacing: 0.5px;
    min-width: 140px;
    padding-left: 10px;
    border-bottom: 1px solid #949494;
    text-align: left;
    position: relative;
    flex-grow: 1;
    &:before {
      content: ' ';
      box-shadow: 0 3px 17px rgba(0, 0, 0, 0.17);
      background-color: #949494;
      background-image: linear-gradient(to top, #5b8c04 0%, #8fb300 100%);
      width: 5px;
      height: 5px;
      border-radius: 50%;
      position: absolute;
      bottom: -3px;
      left: 0; } }
  &__icon {
    flex-shrink: 0;
    margin-left: 5px;
    min-width: 105px;
    text-align: center; } }

.s-cards {}
.s-card {
  background-color: #f3f3f3;
  padding: 30px 15px;
  text-align: center;
  margin-bottom: 30px;
  &__icon {
    max-width: 200px;
    margin: 0 auto 20px;
    height: 100px;
    line-height: 100px; }
  &__tt {
    color: #191919;
    line-height: 24px;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 0.5px;
    margin-bottom: 15px; }
  &__txt {
    color: #191919;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.35px; } }

.accordion {
  &_push-bt {
    margin-bottom: 60px; }
  &-item {
    margin: 10px 0;
    &_opened {
      .accordion-item__dd {
        display: block; }
      .accordion-item__cap {
        .icon {
          background-color: transparent;
          background-image: none;
          box-shadow: none;
          border: 1px solid #609004;
          &:after {
            background-image: url(../img/icons/arrow-top-dark.svg); } } } }

    &__dd {
      margin: 20px 0 0;
      display: none;
      color: #191919;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: 0.35px; }
    &__cap {
      color: #191919;
      font-size: 18px;
      font-weight: 500;
      line-height: 20px;
      letter-spacing: 0.6px;
      background-color: #f3f3f3;
      margin-left: -15px;
      margin-right: -15px;
      padding: 25px 15px;
      span {
        display: inline-block;
        vertical-align: middle;
        position: relative;
        padding-left: 53px; }
      .icon {
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -19px;
        width: 38px;
        height: 38px;
        box-shadow: 0 3px 17px rgba(0, 0, 0, 0.17);
        background-color: #e8e8e8;
        background-image: linear-gradient(to top, #5b8c04 0%, #8fb300 100%);
        border-radius: 50%;
        &:after {
          content: ' ';
          background: url(../img/icons/arrow-down-light.svg) center center no-repeat;
          background-size: 100% 100%;
          width: 12px;
          height: 8px;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%); } } } } }




/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .s-icons-list {
    margin-left: 0;
    margin-right: 0; }

  .s-icons-list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 58px; }

  .s-cards {
    display: flex;
    justify-content: center;
    .s-card {
      flex-basis: 290px;
      max-width: 290px;
      margin-left: 25px;
      margin-right: 25px; } }

  .bodoni-txt_md {
    font-size: 30px;
    line-height: 36px;
    letter-spacing: 0.75px; }

  .main-bl {
    padding: 50px 0;
    &_sm {
      padding: 30px 0; } }

  .accordion {
    &-item {
      &__cap {
        margin-left: 0;
        margin-right: 0;
        padding-left: 40px;
        padding-right: 40px;
        font-size: 24px;
        line-height: 26px;
        letter-spacing: 0.6px; }
      &__dd {
        padding-left: 40px;
        padding-right: 40px; } } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .s-icons-list__item {
    padding: 13px 15px; }
  .accordion {
    &_push-bt {
      margin-bottom: 90px; } }
  .s-box {
    padding-bottom: 50px;
    margin-bottom: 40px; }
  .s-cards {
    margin-top: 40px; }
  .s-card__icon {
    height: 106px;
    line-height: 106px;
    margin-top: 5px;
    margin-bottom: 15px; }
  .s-card__tt {
    margin-bottom: 23px; }
  .s-card {
    padding-bottom: 55px; }
  .accordion-item__cap {
    padding: 28px 60px; }
  .accordion-item {
    margin: 14px 0; }
  .accordion-item__dd {
    margin-top: 34px;
    padding-left: 60px;
    padding-right: 60px;
    margin-bottom: 30px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .banner-catalog_wrapp4.banner-catalog {
    background-position: 75% 0%; }

  /* end media min width 1200 pixels */ }

