/* ----- Review ------------------- */

.stars-st {
  .icon-star {
    width: 14px;
    height: 14px;
    background-position: -410px -280px; }
  .icon-star-2 {
    width: 14px;
    height: 14px;
    background-position: -409px -324px; } }

.review-box {
  background-color: #f7f7f7;
  margin-left: -15px;
  margin-right: -15px;
  padding: 20px 15px;
  margin-bottom: 30px;
  &es {
    margin-bottom: 40px; }
  &__personal {
    margin-bottom: 20px;
    text-align: center; }
  &__img {
    width: 81px;
    height: 81px;
    line-height: 81px;
    text-align: center;
    border-radius: 50%;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    background: #ccc url(../img/review-default.png) center center no-repeat;
    margin-bottom: 20px; }

  &__name {
    color: #1b1b1b;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.4px;
    overflow: hidden;
    text-overflow: ellipsis; }


  &__info {
    display: flex;
    justify-content: space-between;
    line-height: 20px; }

  &__content {
    >p {
      font-weight: 300;
      color: #1a1a1a;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.35px; } } }

.box-rating {
  flex-shrink: 0;
  margin-right: 20px;
  color: #1b1b1b;
  font-size: 16px;
  letter-spacing: 0.4px; }
.review-time {
  text-align: right;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0.35px; }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .box-rating {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .stars-st {
      margin-right: 19px; } }
  .stars-st {
    line-height: 1px;
    .icon {
      margin-right: 1px; } }

  .review-box {
    display: flex;
    justify-content: flex-start;
    &__personal {
      margin-right: 20px;
      flex-shrink: 0;
      flex-basis: 120px;
      max-width: 120px; }
    &__content {
      flex-grow: 1; } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .review-boxes {
    margin-top: 30px;
    margin-bottom: 60px; }
  .review-box {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 35px;
    padding-top: 39px;
    padding-bottom: 30px;
    padding-left: 30px;
    padding-right: 70px;
    &__name {
      font-size: 16px; }
    &__personal {
      flex-basis: 155px;
      max-width: 155px;
      margin-right: 10px; }
    &__content {
      >p {
        font-weight: 300;
        color: #1a1a1a;
        font-size: 14px;
        line-height: 26px;
        letter-spacing: 0.35px;
        margin-bottom: 30px; } } }
  /* end media min width 768 pixels */ }

/* ----- END Review --------------- */
