/* ----- Controls styles ----------------- */
.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: $btn-padding;
  font: $btn-font;
  border: none;
  border-radius: $btn-radius;
  text-align: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  color: $btn-color;
  background-color: $btn-fill-color;
  background-image: linear-gradient(to top, #5b8c04, #8fb300);
  box-shadow: 0px 3px 17px 0 rgba(0, 0, 0, 0.17);
  // transition: ease-in-out 0.15s

  &:hover {
    text-decoration: none;
    color: $btn-color-hover;
    background-color: $btn-fill-hover-color;
    background-image: linear-gradient(to top, lighten(#5b8c04, 5%), darken(#8fb300, 5%)); }

  &_second-st {
    background-color: $btn-fill-second-color;
    background-image: linear-gradient(to top, #6b6b6b 0%, #b1b1b1 100%);

    &:hover {
      background-color: $btn-fill-second-hover-color;
      background-image: linear-gradient(to top, darken(#6b6b6b, 10%) 0%, lighten(#b1b1b1, 10%) 100%); } }

  &_border {
    line-height: 24px;
    background-color: #fff;
    background-image: none;
    color: #1a1a1a;
    letter-spacing: 0.4px;
    position: relative;
    background-image: linear-gradient(to top, #5b8c04, #8fb300);
    z-index: 1;
    box-shadow: 0px 3px 17px 0 rgba(0, 0, 0, 0.17);
    &:before {
      content: ' ';
      position: absolute;
      top: 2px;
      left: 2px;
      bottom: 2px;
      right: 2px;
      z-index: -1;
      background-color: #fff;
      border-radius: 21.1px; }

    &:hover {
      color: $btn-border-hover-color-text;
      background-color: $btn-border-hover-color;
      background-image: linear-gradient(to top, #5b8c04, #8fb300);
      &:before {
        display: none; } }

    &.btn_second-st {
      border-color: $btn-fill-second-color;
      color: $btn-fill-second-color;

      &:hover {
        color: $btn-border-hover-color-text;
        background-color: $btn-fill-second-color; } } }

  // &_dark.btn_border
  //   background-color: #f6f6f6
  //   background-image: none

  &_min-width {
    min-width: $btn-width-min; }

  &_max {
    width: 100%; }

  &_sm {
    padding: $btn-padding-sm;
    font-size: 14px;
    letter-spacing: 0.35px;

    &.btn_min-width {
      min-width: $btn-width-min-sm; } }

  &_md {
    padding: $btn-padding-md;
    font-size: 16px;
    font-weight: normal;

    &.btn_min-width {
      min-width: $btn-width-min-md; } }

  &_lg {
    padding: $btn-padding-lg;
    font-size: 20px;
    &_sm-txt {
      font-size: 18px; }

    &.btn_min-width {
      min-width: $btn-width-min-lg; }

    &.btn_min-width2 {
      min-width: 245px; } }

  &_next {
    &.btn_icon {
      padding: 9px 28px 10px 35px;
      .btn__icon_right {
        margin-left: 20px; }
      .btn__icon_left {
        margin-right: 20px; } } }


  &__icon-text {
    display: inline-block;
    vertical-align: middle; }

  &_md.btn_icon {
    padding: 13px 28px;
    font-size: 20px;
    font-weight: normal; }

  &__icon {
    display: inline-block;
    vertical-align: middle;
    font-style: normal;

    &_right {
      margin-left: 15px; }

    &_left {
      margin-right: 15px; } }

  // &_font-bold
  //   font-family: $btn-font-family-bold
  //   font-weight: $btn-font-weight-bold

  // &_font-light
  //   font-family: $btn-font-family-light
  //   font-weight: $btn-font-weight-light

  // &_size-sm
  //   font-size: $btn-size-sm
  // &_size-md
  //   font-size: $btn-size-md
  // &_size-lg
  //   font-size: $btn-size-lg

  // &_up
  //   text-transform: uppercase

  // &_radius-sm
 }  //   border-radius: $btn-radius-sm

input.btn {
  padding-top: 0;
  padding-bottom: 0;
  height: $input-height;
  width: auto;
  &_max {
    width: 100%; }

  &.btn_lg {
    height: 52px; } }

.form-group-grid__four-col {
  .btn {
    vertical-align: bottom;
    display: block; } }

.placeholder {
  color: $st-color-placeholder; }

input,
textarea {
  @include placeholder-color($st-color-placeholder); }

::-webkit-contacts-auto-fill-button {
  opacity: 0; }

input[type="password"]::-webkit-contacts-auto-fill-button {
  opacity: 1; }

input,
textarea {
  position: relative;
  height: $input-height;
  width: 100%;
  padding: $input-padding;
  border: $input-border;
  font: $input-font;
  color: $input-color;
  background-color: $input-fill-color;
  outline: none;
  border-radius: $input-radius;
  -webkit-appearance: none;
  box-shadow: $input-shadow;

  &:hover {
    border-color: $input-hover-border-color; }

  &:focus {
    border-color: $input-focus-border-color; } }

input[type="radio"], input[type="checkbox"] {
  width: auto;
  margin: 3px 5px 3px 0;
  height: 12px; }

input[type="checkbox"] {
  -webkit-appearance: checkbox; }
input[type="radio"] {
  -webkit-appearance: radio; }

textarea {
  vertical-align: top;
  height: 160px;
  padding: $textarea-padding;
  resize: none; }

/* ----- END controls ------------------- */
