/* ----- Animate ------------------- */

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {

  .anim-hidden {
    opacity: 0; }

  .anim-visible {
    opacity: 1; }

  .animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;

    &.infinite {
      -webkit-animation-iteration-count: infinite;
      animation-iteration-count: infinite; }

    &.delay-halfs {
      -webkit-animation-delay: 0.5s;
      animation-delay: 0.5s; }

    &.delay-300ms {
      -webkit-animation-delay: 300ms;
      animation-delay: 300ms; }

    &.delay-600ms {
      -webkit-animation-delay: 600ms;
      animation-delay: 600ms; }

    &.delay-900ms {
      -webkit-animation-delay: 900ms;
      animation-delay: 900ms; }

    &.delay-1200ms {
      -webkit-animation-delay: 1200ms;
      animation-delay: 1200ms; }

    &.delay-1s {
      -webkit-animation-delay: 1s;
      animation-delay: 1s; }

    &.delay-2s {
      -webkit-animation-delay: 2s;
      animation-delay: 2s; }

    &.fast {
      -webkit-animation-duration: 800ms;
      animation-duration: 800ms; }

    &.faster {
      -webkit-animation-duration: 500ms;
      animation-duration: 500ms; }

    &.slow {
      -webkit-animation-duration: 2s;
      animation-duration: 2s; }

    &.slower {
      -webkit-animation-duration: 3s;
      animation-duration: 3s; } }

  // show-animation bounce-in-left
  // show-animation bounce-in-right
  // show-animation fadeInUp
  // delay-300ms

  .bounce-in-left.animated {
    -webkit-animation-name: bounceInLeft;
    animation-name: bounceInLeft; }

  .bounce-in-right.animated {
    -webkit-animation-name: bounceInRight;
    animation-name: bounceInRight; }

  .fadeInLeft.animated {
    -webkit-animation-name: fadeInLeft;
    animation-name: fadeInLeft; }

  .fadeInRight.animated {
    -webkit-animation-name: fadeInRight;
    animation-name: fadeInRight; }

  .fadeInUp.animated {
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp; }

  .zoomIn.animated {
    -webkit-animation-name: zoomIn;
    animation-name: zoomIn; }

  .pulse.animated {
    -webkit-animation-name: pulse;
    animation-name: pulse; }

  .main-tt__duplicate.show-animation {
    text-indent: 900px;
    white-space: nowrap;
    transition: ease-in-out 1s 0.5s; }
  .animated.main-tt__duplicate {
    text-indent: 0px; }
  .banner-slider-box {
    animation: fadeInUp 1s both; }
  .banner-content-box {
    animation: fadeInLeft 1s both; }
  .menu-item {
    animation: fadeInUp 1s both; }
  .h-social li, .menu-item {
    @for $i from 1 through 12 {
      &:nth-child(#{$i}) {
        -webkit-animation-delay: (100ms * $i) - 100;
        animation-delay: (100ms * $i) - 100; } } }
  .icon-txt {
    @for $i from 1 through 12 {
      &:nth-child(#{$i}) {
        -webkit-animation-delay: (150ms * $i) - 150;
        animation-delay: (150ms * $i) - 150; } } }

  .prod-cards__item.animated {
    animation: fadeInUp 0.5s both;
    @for $i from 1 through 30 {
      &:nth-child(#{$i}) {
        -webkit-animation-delay: (100ms * $i) - 100;
        animation-delay: (100ms * $i) - 100; } } }


  @-webkit-keyframes fadeInDown {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInDown {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @-webkit-keyframes pulse {
    from {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); }

    50% {
      -webkit-transform: scale3d(1.05, 1.05, 1.05);
      transform: scale3d(1.05, 1.05, 1.05); }

    to {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); } }

  @keyframes pulse {
    from {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); }

    50% {
      -webkit-transform: scale3d(1.05, 1.05, 1.05);
      transform: scale3d(1.05, 1.05, 1.05); }

    to {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); } }

  @-webkit-keyframes fadeInRight {
    from {
      opacity: 0;
      -webkit-transform: translate3d(100%, 0, 0);
      transform: translate3d(100%, 0, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInRight {
    from {
      opacity: 0;
      -webkit-transform: translate3d(100%, 0, 0);
      transform: translate3d(100%, 0, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }


  @-webkit-keyframes fadeInLeft {
    from {
      opacity: 0;
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInLeft {
    from {
      opacity: 0;
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }


  @-webkit-keyframes fadeInLeftSt {
    from {
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInLeftSt {
    from {
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }


  @-webkit-keyframes bounceInRight {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

    from {
      opacity: 0;
      -webkit-transform: translate3d(3000px, 0, 0);
      transform: translate3d(3000px, 0, 0); }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(-25px, 0, 0);
      transform: translate3d(-25px, 0, 0); }

    75% {
      -webkit-transform: translate3d(10px, 0, 0);
      transform: translate3d(10px, 0, 0); }

    90% {
      -webkit-transform: translate3d(-5px, 0, 0);
      transform: translate3d(-5px, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes bounceInRight {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

    from {
      opacity: 0;
      -webkit-transform: translate3d(3000px, 0, 0);
      transform: translate3d(3000px, 0, 0); }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(-25px, 0, 0);
      transform: translate3d(-25px, 0, 0); }

    75% {
      -webkit-transform: translate3d(10px, 0, 0);
      transform: translate3d(10px, 0, 0); }

    90% {
      -webkit-transform: translate3d(-5px, 0, 0);
      transform: translate3d(-5px, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }


  @-webkit-keyframes bounceInLeft {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

    0% {
      opacity: 0;
      -webkit-transform: translate3d(-3000px, 0, 0);
      transform: translate3d(-3000px, 0, 0); }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(25px, 0, 0);
      transform: translate3d(25px, 0, 0); }

    75% {
      -webkit-transform: translate3d(-10px, 0, 0);
      transform: translate3d(-10px, 0, 0); }

    90% {
      -webkit-transform: translate3d(5px, 0, 0);
      transform: translate3d(5px, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes bounceInLeft {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

    0% {
      opacity: 0;
      -webkit-transform: translate3d(-3000px, 0, 0);
      transform: translate3d(-3000px, 0, 0); }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(25px, 0, 0);
      transform: translate3d(25px, 0, 0); }

    75% {
      -webkit-transform: translate3d(-10px, 0, 0);
      transform: translate3d(-10px, 0, 0); }

    90% {
      -webkit-transform: translate3d(5px, 0, 0);
      transform: translate3d(5px, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes rotate {
    from {
      transform: rotate(0); }

    to {
      transform: rotate(360deg); } }

  @keyframes rotate-back {
    from {
      transform: rotate(0); }

    to {
      transform: rotate(-360deg); } }


  @-webkit-keyframes zoomIn {
    from {
      opacity: 0;
      -webkit-transform: scale3d(0.3, 0.3, 0.3);
      transform: scale3d(0.3, 0.3, 0.3); }

    50% {
      opacity: 1; } }

  @keyframes zoomIn {
    from {
      opacity: 0;
      -webkit-transform: scale3d(0.3, 0.3, 0.3);
      transform: scale3d(0.3, 0.3, 0.3); }

    50% {
      opacity: 1; } }

  @-webkit-keyframes fadeInUp {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInUp {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes rubberBand {
    from {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); }

    30% {
      -webkit-transform: scale3d(1.25, 0.75, 1);
      transform: scale3d(1.25, 0.75, 1); }

    40% {
      -webkit-transform: scale3d(0.75, 1.25, 1);
      transform: scale3d(0.75, 1.25, 1); }

    50% {
      -webkit-transform: scale3d(1.15, 0.85, 1);
      transform: scale3d(1.15, 0.85, 1); }

    65% {
      -webkit-transform: scale3d(0.95, 1.05, 1);
      transform: scale3d(0.95, 1.05, 1); }

    75% {
      -webkit-transform: scale3d(1.05, 0.95, 1);
      transform: scale3d(1.05, 0.95, 1); }

    to {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); } }
  /* end media min width 1000 pixels */ }

/* ----- END Animate --------------- */
